<template>
  <div id="type4">
    <header>
      <img src="../../assets/imgs/images/pre.png" @click="goback" />
      <span>栖息岛</span>
    </header>
    <section class="content1">
      <div v-for="(item, i) in allData.tj" :key="item.id" class="content1-item">
        <img
          :src="allData.tj[0].list_image"
          class="first-item"
          v-if="i == 0"
          @click="routerPage(item.id)"
        />
        <div class="other-item" v-if="i !== 0" @click="routerPage(item.id)">
          <img :src="item.list_image" />
          <div class="item-title">
            <h3>{{ item.acticity_name }}</h3>
            <p>{{ item.view_count }}人阅读</p>
          </div>
        </div>
      </div>
    </section>
    <section class="content2">
      <ModuleTypeHeader :moduleObjItem="islands"></ModuleTypeHeader>
      <div class="items">
        <div
          class="item"
          v-for="item in allData.ht"
          :key="item.id"
          @click="routerPage(item.id)"
        >
          <img :src="item.list_image" />
          <div class="text">
            <h3>{{ item.acticity_name }}</h3>
            <p>{{ item.view_count }}人阅读</p>
          </div>
        </div>
      </div>
    </section>
    <section class="content3">
      <ModuleTypeHeader :moduleObjItem="collectsSection"></ModuleTypeHeader>
      <div class="items">
        <div
          class="item"
          v-for="item in allData.zj"
          :key="item.id"
          @click="routerPage(item.id)"
        >
          <img :src="item.list_image" />
          <div class="text">
            <h3>
              <span>{{ item.acticity_name }}</span>
              <span>立即参与</span>
            </h3>
            <p>{{ item.view_count }}人阅读</p>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import ModuleTypeHeader from "@/components/ModuleTypeHeader.vue";
export default {
  components: { ModuleTypeHeader },
  data() {
    return {
      allData: [],
      islands: {
        moduleTitle: "岛屿书话",
        moreType: 1,
        type: 20,
      },
      collectsSection: {
        moduleTitle: "征集",
        moreType: 1,
        type: 21,
      },
    };
  },
  created() {
    this.$nextTick(() => {
      this.getAllData();
    });
  },
  methods: {
    goback() {
      this.$router.go(-1);
    },
    async getAllData() {
      let data = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getActivityList",
      });
      this.allData = data.data.data;
    },
    routerPage(id) {
      this.$router.push({
        path: "/type4-item",
        query: {
          id,
        },
      });
    },
  },
};
</script>

<style lang="scss" scoped>
#type4 {
  header {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 999;
    width: 100%;
    padding: 10px 0;
    background-color: #fff;
    overflow: hidden;
    margin-bottom: 10px;
    border-bottom: 1px solid var(--theme-french-gray);
    img {
      width: 10px;
      margin-left: 15px;
    }
    span {
      width: 100%;
      text-align: center;
    }
  }
  .content1 {
    padding: 60px 15px 0px 15px;
    width: 100%;
    border-bottom: 5px solid var(--theme-title-bottom);
    .content1-item {
      width: 100%;
      margin-bottom: 10px;
      .first-item {
        border-radius: 8px;
        width: 100%;
      }
      .other-item {
        padding: 15px 0;
        width: 100%;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        &:not(:last-child) {
          border-bottom: 1px solid var(--theme-line-bottom);
        }
        img {
          flex: 0;
          width: 40%;
          border-radius: 5px;
        }
        .item-title {
          flex: 1;
          margin-left: 15px;
          h3 {
            font-size: 15px;
            font-weight: 500;
          }
          p {
            margin-top: 8px;
            font-size: 12px;
            color: var(--theme-gray);
          }
        }
      }
    }
  }
  .content2 {
    border-bottom: 10px solid var(--theme-french-gray);
    .items {
      padding: 0 15px;
      .item {
        height: 105px;
        width: 100%;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        padding: 15px 0;
        border-bottom: 1px solid var(--theme-line-bottom);
        img {
          flex: 0;
          width: 20%;
          height: 100%;
          border-radius: 5px;
        }
        .text {
          flex: 1;
          margin-left: 15px;
          h3 {
            font-size: 15px;
            font-weight: 500;
            line-height: 1.5em;
          }
          p {
            margin-top: 8px;
            font-size: 12px;
            color: var(--theme-gray);
          }
        }

        &:last-child {
          border-bottom: 0px;
        }
      }
    }
  }
  .content3 {
    padding-bottom: 60px;
    .items {
      width: 100%;
      padding: 0 15px;
      .item {
        border-radius: 8px;
        box-shadow: 0px 0px 8px var(--theme-shadow);
        img {
          width: 100%;
        }
        .text {
          padding: 15px;
          h3 {
            font-size: 15px;
            font-weight: 500;
          }
          p {
            font-size: 12px;
            margin-top: 5px;
            color: var(--theme-gray);
          }
        }
      }
    }
  }
}
</style>