<template>
  <div id="detail-music">
    <header>
      <van-nav-bar
        title="聆听诗意"
        class="nav-content"
        @click-left="clickLeft"
        @click-right="clickRight"
      >
        <template #left>
          <img src="../../assets/imgs/images/pre.png" />
        </template>
        <template #right>
          <img src="../../assets/imgs/images/share.png" />
          <van-share-sheet
            v-model="showShare"
            :options="options"
            get-container="#detail-music"
          />
        </template>
      </van-nav-bar>
    </header>
    <div class="main-music" ref="musicBgc">
      <div class="bgc-blur">
        <div class="music-img-box">
          <img
            v-if="musicData.program_type == 2"
            :src="musicData.str_img_url"
          />
          <div class="other-music-box" v-if="musicData.program_type == 1">
            <img :src="musicData.str_img_url" />
          </div>
        </div>
        <p>{{ musicData.str_program_title }}</p>
        <div class="audio-components">
          <p>
            <span class="current">{{ musicCurrentTime }}</span>
            <van-slider
              type="range"
              min="0"
              max="100"
              v-model="value"
              id="progress"
              ref="progress"
              bar-height="2px"
              @change="getProgressValue"
              active-color="var(--theme-green)"
              inactive-color=" var(--theme-gray)"
            />
            <span class="alltime">{{ musicAllTime }}</span>
          </p>
          <p class="music-controls">
            <img
              src="../../assets/imgs/images/program/sxxh.png"
              @click="setMusicload"
            />
            <img
              @click="prevMusic"
              src="../../assets/imgs/images/program/sq.png"
            />
            <span class="play" @click="setMusicIsPlay" ref="play"></span>
            <img
              @click="nextMusic"
              src="../../assets/imgs/images/program/xq.png"
            />
            <img
              @click="showPopupList"
              src="../../assets/imgs/images/program/lb.png"
            />
            <van-popup
              class="show-popup-list"
              v-model="showPopup"
              get-container="#detail-music"
              round
              position="bottom"
              closeable
              close-icon="close"
            >
              <div class="popup-list">
                <h3>
                  播放列表<span>({{ PopupListLength }})</span>
                </h3>
                <div class="music-items">
                  <div
                    class="item"
                    @click="collectToPlayId(item.id)"
                    v-for="item in PopupList"
                    :key="item.id"
                  >
                    <div class="text-play">
                      <span
                        :class="
                          item.id == $route.query.id || item.id == musicId
                            ? 'collect-play'
                            : 'collect-pause'
                        "
                      ></span>
                      <span>{{ item.title }}</span>
                    </div>
                    <img
                      v-if="!(item.id == $route.query.id || item.id == musicId)"
                      @click="removeCollectItem(item.id)"
                      src="../../assets/imgs/images/del3.png"
                    />
                  </div>
                </div>
              </div>
            </van-popup>
          </p>
          <p class="music-share">
            <span
              @click="musicCollect"
              :class="
                musicIsCollectPic == false ? 'true-collect' : 'false-collect'
              "
            ></span>
            <img src="../../assets/imgs/images/program/ds.png" />
            <img src="../../assets/imgs/images/program/xz.png" />
            <img src="../../assets/imgs/images/program/pl.png" />
          </p>
        </div>
      </div>
    </div>
    <div class="other-content">
      <div
        class="re-bgc"
        v-if="
          musicData.recommend_album !== undefined &&
          musicData.recommend_album !== null
        "
      >
        <div class="recommend_book">
          <img :src="recommendAlbum.show_ing_url" />
          <div class="re-book">
            <div>
              <h3>{{ recommendAlbum.album_name }}</h3>
              <p>{{ recommendAlbum.remark }}</p>
            </div>
            <p>{{ recommendAlbum.view_count }}人阅读</p>
          </div>
        </div>
      </div>
      <div
        class="music-author"
        v-if="
          musicData.str_readpeam !== undefined &&
          musicData.str_readpeam !== null
        "
      >
        <div
          class="author-item"
          v-for="item in musicData.str_readpeam"
          :key="item.gid"
        >
          <div class="author-pic">
            <img :src="item.img_new" />
          </div>
          <div class="author-play">
            <p>{{ item.g_name }}</p>
            <span>{{ item.title }}</span>
          </div>
        </div>
      </div>

      <div
        class="music-poem"
        v-if="
          musicData.str_peam_title !== undefined &&
          musicData.str_peam_title !== null
        "
      >
        <p>{{ musicData.str_peam_title }}</p>
        <p>{{ musicData.str_autor_info }}</p>
        <p v-for="(item, i) in musicPoem" :key="i">{{ item }}</p>
      </div>
      <div
        class="poem-share"
        v-if="
          musicData.str_poetry_enjoy !== undefined &&
          musicData.str_poetry_enjoy !== null
        "
      >
        <h3>- 诗享 -</h3>
        <p>Poetry Review</p>
        <div>
          <p v-for="(item, i) in poemPoetryEnjoy" :key="i">{{ item }}</p>
        </div>
      </div>
      <div
        class="pic-share"
        v-if="
          musicData.str_picture_enjoy !== undefined &&
          musicData.str_picture_enjoy !== null
        "
      >
        <h3>-图享-</h3>
        <p>Cover</p>
        <div>
          <img :src="musicData.str_picture_url" />
          <span>{{ musicData.str_picture_enjoy }}</span>
        </div>
      </div>
      <div
        class="happy-share"
        v-if="
          musicData.str_music_enjoy !== undefined &&
          musicData.str_music_enjoy !== null
        "
      >
        <h3>-乐享-</h3>
        <p>Soundtrack</p>
        <div>{{ musicData.str_music_enjoy }}</div>
      </div>
    </div>
    <div
      class="content"
      v-html="text"
      v-if="
        musicData.rich_content !== undefined && musicData.rich_content !== null
      "
    ></div>
  </div>
</template>

<script>
export default {
  props: {
    duration: {
      type: [Number, String],
      default: "00:00:00",
    },
    picIsPlay: Boolean,
    currentTime: Number,
    musicId: [Number, String],
  },
  data() {
    return {
      musicIsCollectPic: false,
      musicData: {},
      showShare: false,
      value: 0,
      musicIsPlay: this.picIsPlay,
      recommendAlbum: {},
      poemPoetryEnjoy: [],
      musicPoem: [],
      text: "",
      showPopup: false,
      PopupList: [],
      PopupListLength: 0,
      options: [
        { name: "微信", icon: "wechat" },
        { name: "朋友圈", icon: "wechat-moments" },
        { name: "QQ", icon: "qq" },
      ],
    };
  },
  created() {
    this.getMusicData();
    this.getCollectListData();
    this.$nextTick(() => {
      this.setPlayandPause();
      this.musicRenderCollect();
    });
  },
  computed: {
    musicCurrentTime() {
      this.setValue();
      this.setClassName();
      this.setPlayandPause();
      return this.getMusicFormatTime(this.currentTime);
    },
    musicAllTime() {
      return this.getMusicFormatTime(this.duration);
    },
    reRenderPage() {
      return this.getMusicInfoData(this.musicId);
    },
  },
  watch: {
    musicIsCollectPic() {
      this.getCollectListData();
    },
    musicId(newId) {
      this.getMusicInfoData(newId);
    },
  },
  methods: {
    showPopupList() {
      this.showPopup = !this.showPopup;
    },
    collectToPlayId(id) {
      this.$emit("collect-to-play-id", id);
      let self = this;
      let timer = setTimeout(function () {
        self.getMusicInfoData(id);
        clearTimeout(timer);
      }, 1000);

      this.$route.query.id = id;
    },
    prevMusic() {
      if (localStorage.poemMusicIsCollect !== undefined) {
        let arr = JSON.parse(localStorage.getItem("poemMusicIsCollect"));
        let arrId = [];
        arr.map((item) => {
          arrId.push(Number(item.id));
        });
        let index = arrId.indexOf(this.musicId);
        if (index !== -1) {
          let id = index == 0 ? arrId[index] : arrId[index - 1];
          if (index == 0) {
            this.$toast({
              message: "没有更多了",
              position: "bottom",
            });
          } else {
            this.collectToPlayId(id);
            this.getMusicInfoData(id);
          }
        } else {
          let id = arrId[0];
          this.collectToPlayId(id);
          this.getMusicInfoData(id);
        }
      } else {
        this.$toast({
          message: "没有更多了",
          position: "bottom",
        });
      }
    },
    nextMusic() {
      if (localStorage.poemMusicIsCollect !== undefined) {
        let arr = JSON.parse(localStorage.getItem("poemMusicIsCollect"));
        let arrId = [];
        arr.map((item) => {
          arrId.push(Number(item.id));
        });
        let index = arrId.indexOf(this.musicId);
        if (index !== -1) {
          let id = index == arrId.length - 1 ? arrId[index] : arrId[index + 1];
          if (index == arrId.length - 1) {
            this.$toast({
              message: "没有更多了",
              position: "bottom",
            });
          } else {
            this.collectToPlayId(id);
          }
        } else {
          let id = arrId[arrId.length - 1];
          this.$route.query.id = id;
          this.collectToPlayId(id);
          this.getMusicInfoData(id);
        }
      } else {
        this.$toast({
          message: "没有更多了",
          position: "bottom",
        });
      }
    },
    getCollectListData() {
      if (localStorage.poemMusicIsCollect !== undefined) {
        this.PopupList = JSON.parse(localStorage.getItem("poemMusicIsCollect"));
        this.PopupListLength = this.PopupList.length;
      }
    },
    setClassName() {
      if (
        this.$refs.play !== undefined &&
        this.$refs.play.className == "play" &&
        this.currentTime == this.duration
      ) {
        this.$refs.play.className = "pause";
        this.$emit("send-tabbar-play", false);
      }
    },
    setPlayandPause() {
      this.$nextTick(function () {
        if (this.$refs.play !== undefined && !this.picIsPlay) {
          this.$refs.play.className = "pause";
        } else if (this.$refs.play !== undefined && this.picIsPlay) {
          this.$refs.play.className = "play";
        }
      });
    },
    clickLeft() {
      this.$router.go(-1);
    },
    clickRight() {
      this.showShare = true;
    },
    getMusicData() {
      if (this.$route.query.id !== undefined) {
        this.$emit("send-tabbar-music-id", this.$route.query.id);

        this.getMusicInfoData(this.$route.query.id);
      } else if (this.musicId !== undefined) {
        this.$emit("play-music");
        this.getMusicInfoData();
      }
    },
    async getMusicInfoData(id) {
      let url = "";
      if (
        this.musicId.toString().length == 6 ||
        (id !== undefined && id.toString().length == 6)
      ) {
        url = "https://app.thepoemforyou.com/api/v1/getUserProgramInfo";
      } else {
        url = "https://app.thepoemforyou.com/api/v1/getProgramInfo";
      }
      let data = await this.$axios({
        method: "post",
        url: url,
        data: {
          id: id ?? this.musicId,
        },
      });
      this.musicData = data.data.data;
      this.MusicFormat();
      if (this.musicData.program_type !== 2) {
        this.$refs.musicBgc.style.backgroundImage = `url(${this.musicData.str_img_url})`;
      } else {
        this.$refs.musicBgc.style.backgroundImage = "none";
      }
    },
    MusicFormat() {
      this.musicPoem =
        this.musicData.str_peam_content !== undefined &&
        this.musicData.str_peam_content !== null
          ? this.musicData.str_peam_content.split("\n")
          : "";
      if (
        this.musicData.recommend_album !== undefined &&
        this.musicData.recommend_album !== null
      ) {
        this.recommendAlbum = this.musicData.recommend_album;
      }
      this.poemPoetryEnjoy =
        this.musicData.str_poetry_enjoy !== undefined &&
        this.musicData.str_poetry_enjoy !== null
          ? this.musicData.str_poetry_enjoy.split("\n")
          : "";
      if (
        this.musicData.rich_content !== undefined &&
        this.musicData.rich_content !== null
      ) {
        this.text = this.musicData.rich_content.content;
      }

      let index = this.text.indexOf('<img class="rich_pages"');
      let index1 = this.text.indexOf("/>");
      let t = this.text.substring(index, index1);
      let index2 = t.indexOf("width:");
      let index3 = t.indexOf("px", index2);
      let text1 = t.substring(index2, index3);
      let rep = t.replace(text1, "width:330");
      this.text = this.text.replace(t, rep);
    },
    getMusicFormatTime(str) {
      let hours = parseInt(str / 60 / 60);
      hours = hours > 10 ? hours : "0" + hours;
      let minutes = parseInt(str / 60);
      minutes = minutes > 10 ? minutes : "0" + minutes;
      let seconds = parseInt(str % 60);
      seconds = seconds > 10 ? seconds : "0" + seconds;
      str = `${hours}:${minutes}:${seconds}`;
      return str;
    },
    setValue() {
      this.value = parseInt((this.currentTime / this.duration) * 100);
    },
    getProgressValue() {
      this.$emit("send-Tabbar-progress", this.value);
    },
    setMusicIsPlay(e) {
      this.musicIsPlay = !this.musicIsPlay;
      if (this.musicIsPlay && !isNaN(this.duration)) {
        e.target.className = "play";
        this.$emit("send-tabbar-play", true);
      } else if (!this.musicIsPlay && !isNaN(this.duration)) {
        e.target.className = "pause";
        this.$emit("send-tabbar-play", false);
      }
    },
    setMusicload() {
      this.$emit("send-tabbar-load");
    },
    musicCollect() {
      this.musicIsCollectPic = !this.musicIsCollectPic;
      let num = JSON.parse(localStorage.getItem("poemMusicIsCollect"));
      if ((num == null || num == undefined) && this.musicIsCollectPic == true) {
        let obj = {
          id:
            this.$route.query.id !== undefined
              ? this.$route.query.id
              : this.musicId,
          title: this.musicData.str_program_title,
        };
        let ids = JSON.stringify([obj]);
        localStorage.setItem("poemMusicIsCollect", ids);
      } else if (num !== null && this.musicIsCollectPic == true) {
        let arr = JSON.parse(localStorage.getItem("poemMusicIsCollect"));
        arr = arr.filter((item) => {
          if (item.id == this.$route.query.id) {
            return false;
          } else {
            return true;
          }
        });
        let ids = [
          {
            id:
              this.$route.query.id !== undefined
                ? this.$route.query.id
                : this.musicId,
            title: this.musicData.str_program_title,
          },
          ...arr,
        ];
        ids = JSON.stringify(ids);
        localStorage.setItem("poemMusicIsCollect", ids);
      } else if (this.musicIsCollectPic == false) {
        let arr = JSON.parse(localStorage.getItem("poemMusicIsCollect"));
        arr = arr.filter((item) => {
          if (item.id == this.$route.query.id) {
            return false;
          } else {
            return true;
          }
        });
        arr = JSON.stringify(arr);
        localStorage.setItem("poemMusicIsCollect", arr);
      }
    },
    removeCollectItem(id) {
      let arr = JSON.parse(localStorage.getItem("poemMusicIsCollect"));
      arr = arr.filter((item) => {
        if (item.id == id) {
          return false;
        } else {
          return true;
        }
      });
      localStorage.setItem("poemMusicIsCollect", JSON.stringify(arr));
      this.getCollectListData();
    },
    musicRenderCollect() {
      let arr = JSON.parse(localStorage.getItem("poemMusicIsCollect"));
      if (arr !== null) {
        if (this.$route.query.id !== undefined) {
          arr.map((item) => {
            if (item.id == this.$route.query.id) {
              this.musicIsCollectPic = true;
            }
          });
        } else if (this.musicId !== undefined) {
          arr.map((item) => {
            if (item.id == Number(this.musicId)) {
              this.musicIsCollectPic = true;
            }
          });
        } else {
          this.musicIsCollectPic = false;
        }
      }
    },
  },
};
</script>

<style lang="scss" scoped>
@font-face {
  font-family: "fzqk";
  src: url("../../assets/imgs/fzqk.TTF");
  font-family: "dcloud_iconfont";
  src: url("../../assets/fonts/dcloud_iconfont.ttf");
}
#detail-music {
  header {
    padding: 0 5px;
    position: sticky;
    z-index: 999;
    top: 0;
    left: 0;
    height: 50px;
    background-color: #fff;
    .nav-content {
      .van-nav-bar__left img {
        width: 10px;
      }
      .van-nav-bar__right img {
        width: 20px;
      }
    }
  }
  &::v-deep .show-popup-list {
    .popup-list {
      padding-top: 20px;
      h3 {
        font-size: 15px;
        padding: 0 20px;
        span {
          margin-left: 5px;
          font-size: 13px;
          color: var(--theme-gray);
        }
      }
      .music-items {
        margin-top: 15px;
        padding: 20px;
        .item {
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: space-between;
          padding: 10px 0;
          border-top: 1px solid var(  --theme-line-bottom);
          .text-play {
            width: 90%;
            display: flex;
            flex-direction: row;
            align-items: center;
            span {
              &:first-child {
                align-items: baseline;
                display: inline-block;
                margin-right: 10px;
                width: 25px;
                height: 20px;
                background-repeat: no-repeat;
                background-size: contain;
              }
              &:last-child {
                width: 90%;
                font-size: 13px;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
              }
            }
            .collect-play {
              background-image: url(../../assets/imgs/images/zanting.png);
            }
            .collect-pause {
              background-image: url(../../assets/imgs/images/begin7.png);
            }
          }
          img {
            margin-left: 10px;
            width: 10px;
          }
        }
      }
    }
  }
  header {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    .nav-content {
      .van-nav-bar__left img {
        width: 10px;
      }
      .van-nav-bar__right img {
        width: 20px;
      }
    }
  }
  .main-music {
    z-index: 0;
    position: relative;
    width: 100vw;
    background-size: cover;
    .bgc-blur {
      background-color: rgba(255, 255, 255, 0.7);
      .music-img-box {
        width: 100%;
        height: 280px;
        img {
          width: 100%;
          height: 280px;
        }
      }
    }
    .other-music-box {
      position: absolute;
      top: 15%;
      left: 50%;
      transform: translateX(-50%);
      width: 150px;
      height: 150px;
      border-radius: 999px;
      overflow: hidden;
      border: 5px solid var(--theme-gray);
      img {
        height: 150px;
        width: 150px;
        border-radius: 999px;
        overflow: hidden;
      }
    }
  }
  p {
    text-align: center;
    margin: 15px 15px 20px;
  }
  .audio-components {
    z-index: 3;
    width: 100vw;
    p {
      font-size: 12px;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      .current {
        margin: 0 5px;
      }
      .alltime {
        margin: 0 5px 0 0;
      }
      #progress {
        margin: 0 10px;
        display: inline-block;
        width: 60%;
        height: 3px;
        border-radius: 999px;
      }
    }
    .music-controls {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-evenly;
      height: 50px;
      img {
        width: 20px;
      }
      .play {
        width: 50px;
        height: 50px;
        background-image: url(../../assets/imgs/images/program/zt_03.png);
        background-size: contain;
      }
      .pause {
        width: 50px;
        height: 50px;
        background-image: url(../../assets/imgs/images/program/bf.png);
        background-size: contain;
      }
    }
    .music-share {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      height: 50px;
      span {
        width: 20px;
        height: 20px;
        background-size: contain;
        background-repeat: no-repeat;
        margin: 0 20px;
      }
      .true-collect {
        background-image: url(../../assets/imgs/images/program/sc.png);
      }
      .false-collect {
        background-image: url(../../assets/imgs/images/fabuloused.png);
      }
      img {
        width: 20px;
        margin: 0 20px;
      }
    }
  }
  .other-content {
    width: 100vw;
    .re-bgc {
      width: 100%;
      padding: 20px 15px 25px;
      .recommend_book {
        width: 100%;
        box-shadow: 0px 0px 8px var(--theme-gray);
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        padding: 10px;
        img {
          width: 45%;
        }
        .re-book {
          width: 55%;
          margin-left: 15px;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          align-items: start;
          div {
            width: 80%;
            h3 {
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
              font-weight: bold;
              font-size: 18px;
            }
            p {
              margin-top: 5px;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
              color: var(--theme-gray);
              font-size: 15px;
            }
          }
          p {
            margin: 0;
            padding: 0;
            color: var(--theme-deep-gray);
            font-size: 18px;
          }
        }
      }
    }
    .music-author {
      margin-top: 10px;
      .author-item {
        margin: 20px 20px;
        display: flex;
        flex-direction: row;
        align-items: center;
        .author-pic {
          width: 50px;
          height: 50px;
          border-radius: 999px;
          overflow: hidden;
          img {
            width: 50px;
            height: 50px;
          }
        }
        .author-play {
          margin-left: 15px;
          p {
            margin: 0;
            padding: 0;
            text-align: left;
            font-size: 15px;
          }
          span {
            margin-top: 5px;
            font-size: 12px;
            color: var(--theme-gray);
          }
        }
      }
    }
    .music-poem {
      padding: 0 20px;
      p {
        text-align: left;
        font-family: fzqk;
        margin: 15px 0;
        &:nth-of-type(2) {
          font-size: 13px;
