<template>
  <div id="app">
    <TabbarBottom
      @get-music-Id="getMusicId"
      :duration="duration"
      :currentTime="currentTime"
      @send-App-progress="setMuiscProgress"
      @send-app-play="setMusicPlay"
      @send-app-load="setMusicLoad"
      :musicId="musicId"
      :appMusicId="musicId"
      :picIsPlay="picIsPlay"
    ></TabbarBottom>
    <audio ref="audio" />
  </div>
</template>
<script>
import TabbarBottom from "./components/TabbarBottom.vue";
export default {
  data() {
    return {
      musicId: "",
      musicData: {},
      duration: 0,
      currentTime: 0,
      picIsPlay: false,
    };
  },
  components: { TabbarBottom },
  methods: {
    getMusicId(id) {
      if (id !== undefined && typeof this.musicId !== String) {
        if (this.musicId !== id) {
          this.musicId = id;
          this.getMusicInfoData();
        }
      }
    },
    async getMusicInfoData() {
      let url = "";
      if (this.musicId.toString().length == 6) {
        url = "https://app.thepoemforyou.com/api/v1/getUserProgramInfo";
      } else {
        url = "https://app.thepoemforyou.com/api/v1/getProgramInfo";
      }
      let data = await this.$axios({
        method: "post",
        url: url,
        data: {
          id: this.musicId,
        },
      });
      this.musicData = data.data.data;
      this.$refs.audio.src = this.musicData.str_audio_url;
      if (this.$refs.audio) {
        this.$refs.audio.play();
        this.picIsPlay = true;
        let self = this;
        this.$refs.audio.ontimeupdate = function () {
          self.duration = this.duration;
          self.currentTime = this.currentTime;
          if (this.duration == this.currentTime) {
            this.picIsPlay = false;
          }
        };
        let recordMusicId = this.musicId;
        this.$refs.audio.onended = function () {
          let arr = JSON.parse(localStorage.getItem("poemMusicIsCollect"));
          arr = arr.map((item) => {
            return Number(item.id);
          });
          let index = arr.indexOf(Number(recordMusicId));
          if (index !== -1 && arr.length >= 2) {
            if (index == arr.length - 1) {
              self.musicId = arr[0];
              self.getMusicInfoData();
            } else {
              self.musicId = arr[index + 1];
              self.getMusicInfoData();
            }
          } else if (index == -1) {
            self.musicId = arr[0];
            self.getMusicInfoData();
          }
        };
      }
    },
    setMuiscProgress(num) {
      this.$refs.audio.currentTime = (num / 100) * this.duration;
    },
    setMusicPlay(boo) {
      if (boo) {
        this.$refs.audio.play();
        this.picIsPlay = true;
      } else {
        this.$refs.audio.pause();
        this.picIsPlay = false;
      }
    },
    setMusicLoad() {
      this.$refs.audio.load();
      this.$refs.audio.play();
      this.picIsPlay = true;
    },
  },
};
</script>
