<template>
  <div id="type1">
    <header>
      <img src="../../assets/imgs/images/pre.png" @click="goback" />
      <span>动听榜</span>
    </header>
    <div class="banner">
      <van-swipe
        :autoplay="3000"
        :height="156"
        indicator-color="var(--theme-gray)"
      >
        <van-swipe-item v-for="(image, index) in banners" :key="index">
          <img v-lazy="image.image_url" />
        </van-swipe-item>
      </van-swipe>
    </div>
    <div class="usersInfo">
      <h3>
        <img src="../../assets/imgs/images/listIcon.png" />
        来自用户们的读诗佳作
      </h3>
      <ul class="ul">
        <li
          v-for="(item, i) in users"
          :key="item.id"
          @click="routerPage(item.id)"
        >
          <div class="li-left">
            <div class="number">
              <div class="num-pic">
                <div class="text">{{ index(i) }}</div>
                <div class="pic-process">
                  <van-circle
                    v-if="i < 3"
                    v-model="currentRate"
                    :rate="75"
                    :speed="100"
                    layer-color="orange"
                    color="var(--theme-french-gray)"
                    size="60px"
                    stroke-width="50"
                  />
                  <img
                    class="user-pic"
                    v-if="item.headerimgurl !== null"
                    :src="item.headerimgurl"
                  />
                  <img
                    class="system-pic"
                    v-if="item.headerimgurl == null"
                    src="../../assets/imgs/images/userAvatar1.png"
                  />
                </div>
              </div>
              <div class="content">
                <h3>{{ item.username }}</h3>
                <p>{{ item.title }}</p>
                <p>{{ item.author }}</p>
              </div>
            </div>
          </div>
          <div class="li-right">
            <span>{{ item.goods_count }}</span>
            <img src="../../assets/imgs/images/xh.png" />
          </div>
        </li>
      </ul>
    </div>
    <div class="botton">
      <button>去读诗</button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      banners: "",
      users: [],
      currentRate: 0,
    };
  },
  created() {
    this.$nextTick(() => {
      this.getBigPic();
      this.getUserInfo();
    });
  },
  computed: {
    text() {
      return this.currentRate.toFixed(0) + "%";
    },
  },
  methods: {
    goback() {
      this.$router.go(-1);
    },
    async getBigPic() {
      let data = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getBannerList",
        data: {
          position: 5,
        },
      });
      this.banners = data.data.data;
    },
    async getUserInfo() {
      let data = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getUserProgramListOrder",
        data: {
          page: 1,
          limit: 12,
          str: "",
        },
      });
      this.users = data.data.data.data;
    },
    index(i) {
      return (i = i + 1 < 10 ? "0" + (i + 1) : i + 1);
    },
    routerPage(id) {
      this.$router.push({
        path: "/detailMusic",
        query: {
          id,
        },
      });
    },
  },
};
</script>

<style lang="scss" scoped>
@font-face {
  font-family: "fzqk";
  src: url("../../assets/imgs/fzqk.TTF");
  font-family: "dcloud_iconfont";
  src: url("../../assets/fonts/dcloud_iconfont.ttf");
}
#type1 {
  header {
    padding: 30px 20px;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 50px;
    padding: 10px 0;
    background-color: #fff;
    overflow: hidden;
    margin-bottom: 10px;
    border-bottom: 1px solid var(--theme-line-bottom);
    img {
      width: 10px;
      margin-left: 15px;
    }
    span {
      width: 100%;
      text-align: center;
    }
  }
  .banner {
    margin-top: 50px;
    width: 100%;
    height: 156px;
    overflow: hidden;
    img {
      width: 100%;
    }
  }
  .usersInfo {
    margin-bottom: 80px;
    padding: 10px 20px;
    h3 {
      font-size: 15px;
      font-family: fzqk;
      img {
        vertical-align: middle;
        width: 40px;
        margin-right: 5px;
      }
    }

    .ul {
      li {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        font-size: 12px;
        margin: 15px 0;
        &:not(:last-child) {
          border-bottom: 1px solid var(--theme-line-bottom);
        }
        .li-left {
          width: 100%;
          flex: 1;
          display: flex;
          flex-direction: row;
          .number {
            display: flex;
            flex-direction: row;
            .num-pic {
              flex: 1;
              display: flex;
              align-items: center;
              flex-direction: row;
              .pic-process {
                margin-left: 10px;
                img {
                  width: 50px;
                  height: 50px;
                  border-radius: 999px;
                }
              }
              .text {
                font-size: 15px;
                color: #000;
              }
            }
            .content {
              margin-left: 10px;
              h3 {
                font-size: 15px;
              }
              p {
                margin-top: 5px;
                line-height: 1.5em;
                font-size: 12px;
                color: var(--theme-gray);
                &:first-child {
                  font-size: 12px;
                  color: var(--theme-deep-gray);
                }
              }
            }
          }
        }
        &:nth-child(-n + 3) {
          .li-left {
            .pic-process {
              position: relative;
            }
            .num-pic .text {
              font-size: 14px;
              color: var(--theme-gray);
            }
            .user-pic,
            .system-pic {
              position: absolute;
              top: 5px;
              left: 6px;
            }
            img {
              width: 60px;
              height: 60px;
              border-radius: 999px;
            }
          }
        }
        .li-right {
          color: #66a873;
          img {
            margin-left: 10px;
            vertical-align: middle;
          }
        }
      }
    }
  }
  .botton {
    width: 100%;
    position: fixed;
    bottom: 0;
    left: 0;
    text-align: center;
    background-color: #fff;
    height: 70px;
    button {
      width: 50%;
      height: 50px;
      margin: 10px 0;
      border-radius: 999px;
      border: 0px;
      color: #fff;
      background-color: var(--theme-green);
    }
  }
}
</style>