<template>
  <div id="present">
    <header>
      <img src="../../assets/imgs/images/pre.png" @click="goback" />
      <p>为你读诗·诗意空间</p>
    </header>
    <div>
      <iframe
        :src="openWindow"
        frameborder="0"
        width="100%"
        :style="{ height: calHeight }"
        scrolling="auto"
        class="newpage"
      ></iframe>
    </div>
  </div>
</template>
<script>
export default {
  name: "PresentView",
  data() {
    return {
      openWindow:
        "https://shop1617333.m.youzan.com/v2/showcase/homepage?kdt_id=1425165",
    }
  },
  computed: {
    //计算属性 , 设置iframe高度为窗口高度少100px
    calHeight() {
      return window.innerHeight - 100 + "px";
    },
  },

  methods: {
    goback() {
      this.$router.go(-1);
    },
  },
};
</script>
<style lang="scss" scoped>
#present {
  header {
    position: sticky;
    top: 0;
    left: 0;
    padding: 10px 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    img {
      width: 10px;
    }
    p {
      width: 100%;
      font-size: 15px;
      text-align: center;
    }
  }
  iframe {
    width: 108%;
    overflow-x: hidden;
  }
}
</style>