
<template>
  <div id="type3">
    <header>
      <img src="../../assets/imgs/images/pre.png" @click="goback" />
      <span>往期节目</span>
    </header>
    <div class="all-item">
      <div class="item" v-for="item in allData" :key="item.id" @click="routerDetailPage(item.id)">
        <div class="img">
          <img :src="item.str_img_url" />
          <img class="play" src="../../assets/imgs/images/followIsBegin1.png" />
        </div>
        <div class="text">
          <h3>
            {{ item.n_periods_num }}期 |
            {{ item.str_program_title }}
          </h3>
          <span> 阅读{{ item.view_count }}次 </span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      allData: [],
    };
  },
  created() {
    this.$nextTick(()=>{
      this.getAllData();
    })
  },
  methods: {
    goback() {
      this.$router.go(-1);
    },
    async getAllData() {
      let data = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getProgramListApp",
        data: {
          str: "",
          page: 1,
          limit: 20,
        },
      });
      this.allData = data.data.data.data;
    },
    routerDetailPage(id) {
      this.$router.push({
        path: "/detailMusic",
        query: {
          id,
        },
      });
    },
  },
};
</script>

<style lang="scss" scoped>
#type3 {
  header {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 999;
    width: 100%;
    height: 50px;
    padding: 0 20px;
    background-color: #fff;
    overflow: hidden;
    margin-bottom: 10px;
    border-bottom: 1px solid var(--theme-french-gray);
    img {
      width: 10px;
      // margin-left: 15px;
    }
    span {
      width: 100%;
      text-align: center;
    }
  }
  .all-item {
    margin: 60px 0;
    padding: 0 15px;
    .item {
      width: 100%;
      height: 90px;
      margin: 15px 0;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      .img {
        position: relative;
        width: 75px;
        height: 90px;
        overflow: hidden;
        img {
          &:nth-of-type(1) {
            width: 100%;
            height: 100%;
          }
          &:nth-of-type(2) {
            position: absolute;
            left: 35%;
            top: 37%;
            width: 25px;
            background-color: #fff;
            border-radius: 999px;
          }
        }
      }
      .text {
        width: 70%;
        margin: 0 15px;
        h3 {
          font-weight: 500;
          font-size: 15px;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
          margin-bottom: 5px;
        }
        span {
          font-size: 13px;
          color: var(--theme-gray);
        }
      }
    }
  }
}
</style>