<template>
  <div id="load-app">
    <header>
      <img src="../../assets/imgs/images/pre.png" @click="goback" />
      <p>为你读诗Pro</p>
    </header>
    <div>
      <iframe
        :src="openWindow"
        frameborder="0"
        width="100%"
        :style="{ height: calHeight }"
        scrolling="auto"
        class="newpage"
      ></iframe>
    </div>
  </div>
</template>

<script>
export default {
  name: "LoadApp",
  data() {
    return {
      openWindow:
        "https://a.app.qq.com/o/simple.jsp?pkgname=com.weinidushi.app",
    };
  },
  computed: {
    //计算属性 , 设置iframe高度为窗口高度少100px
    calHeight() {
      return window.innerHeight - 100 + "px";
    },
  },

  methods: {
    goback() {
      this.$router.go(-1);
    },
  },
};
</script>

<style lang="scss" scoped>
#load-app {
    header {
    position: fixed;
    top: 0;
    left: 0;
    padding: 10px 15px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-color: #fff;
    img {
      width: 15px;
    }
    p {
      width: 100%;
      font-size: 18px;
      text-align: center;
    }
  }
  iframe {
    width: 108%;
    overflow-x: hidden;
  }
}
</style>