<template>
  <div id="search-section">
    <header v-if="type !== 7">
      <van-nav-bar
        :title="hreaderTitle"
        class="nav-content"
        @click-left="clickLeft"
        @click-right="clickRight"
      >
        <template #left>
          <img src="../../assets/imgs/images/pre.png" />
        </template>
        <template #right>
          <img src="../../assets/imgs/images/share.png" />
          <van-share-sheet
            v-model="showShare"
            :options="options"
            get-container="#search-section"
          />
        </template>
      </van-nav-bar>
    </header>
    <div class="reading-book" v-if="type == 4">
      <div class="search-item">
        <SearchItem
          class="search-item-com"
          :pic="readinHeader.img_new"
          :title="readinHeader.g_name"
          :readingBook="true"
          :canClick="false"
        ></SearchItem>
      </div>
      <div class="old-reader">
        <h3>往期[为您读诗]</h3>
        <div class="search-item">
          <SearchItem
            v-for="item in oldReadinHeader"
            :key="item.id"
            :id="item.id"
            :pic="item.str_img_url"
            :SecondTitle="item.view_count"
            :title="item.str_program_title"
            :readingperson="true"
            :listeningReadingBook="true"
            :type="type"
          ></SearchItem>
        </div>
      </div>
    </div>
    <div class="poem-book" v-if="type == 7">
      <div class="poem-header" @click="clickLeft">
        <div class="poem-bgc">
          <img src="../../assets/imgs/images/返回.png" />
        </div>
        <h3>歌词详情</h3>
      </div>
      <div class="poem-content">
        <h2>{{ poemItemContent.poemTitle }}</h2>
        <p>作者:{{ poemItemContent.poemAuthorName }}</p>
        <div>
          <p v-for="(item, i) in poemItemContent.poemText" :key="i">
            {{ item }}
          </p>
        </div>
      </div>
      <div class="poem-botton">
        <button>朗读</button>
      </div>
    </div>
    <div class="album-info" v-if="type == 3 || type == 2">
      <div class="album-info-header">
        <div class="album-img"><img :src="albumInfo.banner_img_url" /></div>
        <div class="album-content">
          <div class="album-header-title">
            <div class="album-title">
              <h3>{{ albumInfo.album_name }}</h3>
              <p>{{ albumInfo.view_count }}阅读</p>
            </div>
            <div class="collect">
              <img src="../../assets/imgs/images/sc.png" />
            </div>
          </div>
          <p>￥{{ albumInfo.price / 100 }}</p>
          <p>{{ albumInfo.remark }}</p>
        </div>
        <div class="detail-catalogue">
          <van-tabs v-model="detailCatalogueActive" color="var(--theme-green)">
            <van-tab title="详情" v-if="this.albumInfo.rich_content !== null">
              <div v-html="albumRichContent" class="text-pic"></div>
            </van-tab>
            <van-tab title="目录">
              <div class="catalogue-all">
                <div class="catalogue-all-hearder">
                  <div class="catalogue-all-hearder-title">
                    <img src="../../assets/imgs/images/play.png" />
                    <p>
                      全部播放<span>({{ albumCatalogue.length }})</span>
                    </p>
                  </div>
                  <img src="../../assets/imgs/images/up.png" />
                </div>
                <div class="catalogue-all-item-all">
                  <div
                    class="catalogue-all-item"
                    v-for="item in albumCatalogue"
                    :key="item.id"
                    @click="getCatalogueItemDetail(item)"
                  >
                    <div class="item-text">
                      <h3>{{ item.str_program_title }}</h3>
                      <p>{{ item.view_count }}阅读</p>
                    </div>
                    <img
                      v-if="item.free_type == 2"
                      src="../../assets/imgs/images/lock.png"
                    />
                    <span v-if="item.free_type !== 2">试听</span>
                  </div>
                </div>
              </div>
            </van-tab>
          </van-tabs>
        </div>
        <div class="page-button">
          <van-button
            color="#d69472"
            round
            type="info"
            v-if="albumInfo.is_vip_free == 1"
            >VIP免费畅听</van-button
          >
          <van-button color="#59cf61" round type="info"
            >单价:
            <span v-if="albumInfo.price !== null"
              >￥{{ albumInfo.price / 100 }}</span
            >
            <span :class="{ 'line-through': albumInfo.price !== null }"
              >￥{{ albumInfo.line_price / 100 }}</span
            >
          </van-button>
        </div>
      </div>
    </div>
    <div class="video-info" v-if="type == 5">
      <div class="video">
        <video
          class="this-video"
          :src="videoInfo.video_url"
          controls
          :poster="videoInfo.video_img"
        ></video>
        <div class="video-header">
          <h3>{{ videoInfo.video_title }}</h3>
          <img src="../../assets/imgs/images/sc.png" />
        </div>
        <div class="video-content">
          <p>{{ videoInfo.play_count }}人阅读</p>
          <p>{{ videoInfo.video_remark }}</p>
        </div>
      </div>
      <div class="video-evaluate">
        <h3 class="video-evaluate-title">最新评论</h3>
        <div class="video-evaluate-content">
          <div
            class="video-evaluate-content-item"
            v-for="item in videoEvaluate"
            :key="item.id"
          >
            <div class="video-evaluate-left">
              <div class="left-img">
                <img :src="item.headerimgurl" />
                <img
                  class="video-vip"
                  v-if="item.vip == 1"
                  src="../../assets/imgs/images/VIPlogo2.png"
                />
              </div>
            </div>
            <div class="video-evaluate-center">
              <h3>{{ item.username }}</h3>
              <span>{{ item.created }}</span>
              <p>{{ item.content }}</p>
            </div>
            <div class="video-evaluate-right">
              <p>
                <img src="../../assets/imgs/images/nolike.png" />
                <span>{{ item.good_count }}</span>
              </p>
              <div class="more">
                <div @click="showPopup">
                  <img src="../../assets/imgs/images/report.png" />
                </div>
                <van-popup
                  v-model="videoReporShow"
                  round
                  position="bottom"
                  :style="{ height: '20%' }"
                  class="more-content"
                >
                  <p>举报</p>
                  <p @click="upPopup">关闭</p>
                </van-popup>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import SearchItem from "@/components/SearchItem.vue";
export default {
  components: { SearchItem },
  name: "searchSection",
  data() {
    return {
      type: this.$route.params.type,
      oldReadinHeader: [],
      showShare: false,
      readinHeader: {},
      hreaderTitle: "",
      hreaderTitleList: ["读诗人", "专辑详情", "视频详情", "栖息岛"],
      itemId: this.$route.params.id,
      itemValue: this.$route.params.value,
      videoInfo: {},
      videoEvaluate: [],
      videoReporShow: false,
      poemItemContent: {
        poemText: [],
        poemAuthorName: "",
        poemTitle: "",
      },
      albumInfo: {},
      albumRichContent: "",
      albumCatalogue: [],
      firstin: false,
      detailCatalogueActive: 0,
      options: [
        { name: "微信", icon: "wechat" },
        { name: "朋友圈", icon: "wechat-moments" },
        { name: "QQ", icon: "qq" },
      ],
    };
  },
  created() {
    this.$nextTick(() => {
      this.getLinseningBackData();
      this.getPageData();
      this.getLinseningData();
    });
  },
  methods: {
    getLinseningBackData() {
      let obj = {
        type: this.$route.params.type,
        value: this.$route.params.value,
        id: this.$route.params.id,
      };
      if (localStorage.linseingBackData == undefined) {
        localStorage.setItem("linseingBackData", JSON.stringify(obj));
      }

      if (this.$route.params.id == undefined) {
        let locObj = JSON.parse(localStorage.getItem("linseingBackData"));
        this.type = locObj.type;
        this.itemId = locObj.id;
        this.itemValue = locObj.value;
        this.firstin = true;
      }
      if (this.$route.params.id !== undefined) {
        localStorage.setItem("linseingBackData", JSON.stringify(obj));
      }
      if (this.type == 4) {
        this.hreaderTitle = this.hreaderTitleList[0];
      } else if (this.type == 1 || this.type == 2 || this.type == 3) {
        this.hreaderTitle = this.hreaderTitleList[1];
      } else if (this.type == 5) {
        this.hreaderTitle = this.hreaderTitleList[2];
      } else if (this.type == 6) {
        this.hreaderTitle = this.hreaderTitleList[3];
      }
    },
    getPageData() {
      if (this.type == 4) {
        this.getPageDataType4();
      } else if (this.type == 7) {
        this.getPageDataType7();
      }
      else if (this.type == 3 || this.type == 2) {
        this.getPageDataType3and2();
      } else if (this.type == 5) {
        this.getPageDataType5();
      } else if (this.type == 6) {
        this.getPageDataType6();
      }
    },
    async getPageDataType3and2() {
      let url = "";
      if (this.type == 3) {
        url = "https://app.thepoemforyou.com/api/v1/getChildAlbumInfo";
      } else if (this.type == 2) {
        url = "https://app.thepoemforyou.com/api/v1/getAlbumInfo";
      }
      let data = await this.$axios({
        method: "post",
        url: url,
        data: {
          id: this.itemId,
        },
        header: {
          "Content-Type": "application/x-www-form-urlencoded",
        },
      });
      this.albumInfo = data.data.data;
      if (this.albumInfo.rich_content !== null) {
        this.albumRichContent = this.albumInfo.rich_content.content;
      }
      this.albumCatalogue = this.albumInfo.list;
      this.getalbumCatalogueData();
    },
    async getalbumCatalogueData() {
      let data = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getProgramList",
        data: {
          list: this.albumCatalogue,
        },
      });
      this.albumCatalogue = data.data.data.data;
    },
    async getPageDataType5() {
      let data = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getVideoInfo",
        data: {
          id: this.itemId,
        },
      });
      this.videoInfo = data.data.data;
      let dataEvaluate = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getCommentList",
        data: {
          partId: this.itemId,
          page: 1,
          limit: 20,
          str: "",
          type: 5,
        },
      });
      this.videoEvaluate = dataEvaluate.data.data.data;
    },

    async getPageDataType4() {
      let data = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getGuestInfo",
        data: {
          gid: this.itemId,
        },
        header: {
          "Content-Type": "application/x-www-form-urlencoded",
        },
      });
      this.readinHeader = data.data.data;
    },
    async getPageDataType7() {
      let data = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/Search",
        data: {
          type: this.type,
          str: this.itemValue,
          page: 1,
          limit: 20,
        },
        header: {
          "Content-Type": "application/x-www-form-urlencoded",
        },
      });
      this.readinHeader = data.data.data.filter((item) => {
        if (item.id == this.itemId) {
          return true;
        } else {
          return false;
        }
      });
      this.poemItemContent.poemTitle = this.readinHeader[0].Title;
      this.poemItemContent.poemAuthorName = this.readinHeader[0].author_name;
      this.poemItemContent.poemText = this.readinHeader[0].original.split("\n");
    },
    // async getPageDataType3() {
    //   // if (!this.firstin) {
    //   //   this.$router.push({
    //   //     path: "/detailMusic",
    //   //     query: {
    //   //       id: this.itemId,
    //   //     },
    //   //   });
    //   // } else {
    //     let value = this.itemValue;
    //     this.$router.push({
    //       name: "search",
    //       params: {
    //         value,
    //       },
    //     });
    //   // }
    // },
    async getLinseningData() {
      let otherdata = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getGuestProgram",
        data: {
          gid: this.itemId,
          page: 1,
          limit: 10,
          str: "", // this.$route.params.value
        },
      });
      this.oldReadinHeader = otherdata.data.data.data;
    },
    clickLeft() {
      // let value = this.itemValue;
      // this.$router.push({
      //   name: "search",
      //   params: {
      //     value,
      //   },
      // });
      this.$router.back(-1);
    },
    clickRight() {
      this.showShare = true;
    },
    getCatalogueItemDetail(item) {
      if (item.free_type == 1) {
        this.$router.push({
          path: "/detailMusic",
          query: {
            id: item.id,
          },
        });
      } else {
        this.$toast.success("未购买专辑~");
      }
    },
    showPopup() {
      this.videoReporShow = true;
    },
    upPopup() {
      this.videoReporShow = false;
    },
  },
};
</script>
<style lang="scss" scoped>
@font-face {
  font-family: "fzqk";
  src: url("../../assets/imgs/fzqk.TTF");
  font-family: "dcloud_iconfont";
  src: url("../../assets/fonts/dcloud_iconfont.ttf");
}
#search-section {
  header {
    position: sticky;
    z-index: 999;
    top: 0;
    left: 0;
    .nav-content {
      .van-nav-bar__left img {
        width: 10px;
        margin-left: 5px;
      }
      .van-nav-bar__right img {
        width: 20px;
        margin-right: 5px;
      }
    }
  }
  .reading-book {
    margin: 15px;
    .search-item-com {
      padding: 10px;
      border-radius: 10px;
      box-shadow: 0px 0px 8px var(--theme-gray);
    }
    .old-reader {
      h3 {
        margin: 20px 15px;
        font-family: fzqk;
        font-size: 18px;
      }
    }
  }
  .poem-book {
    width: 100%;
    height: 100%;
    background-image: url(../../assets/imgs/images/poemDeatilBg.png);
    .poem-header {
      position: sticky;
      top: 0;
      left: 0;
      width: 100%;
      height: 50px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      font-size: 16px;
      background-image: url(../../assets/imgs/images/poemDeatilBg.png);
      .poem-bgc {
        flex: 0;
        margin: 15px 0px 15px 15px;
        display: inline-block;
        border-radius: 999px;
        width: 30px;
        height: 30px;
        padding: 0 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: rgba(7, 7, 7, 0.2);
        img {
          margin-left: 3px;
          width: 20px;
          height: 20px;
        }
      }
      h3 {
        transform: translateX(-5%);
        flex: 1;
        text-align: center;
        color: darkseagreen;
        font-size: 18px;
      }
    }
    .poem-content {
      margin-bottom: 40px;
      padding: 15px;
      color: #fff;
      font-family: fzqk;
      h2 {
        font-size: 16px;
      }
      p {
        margin-top: 15px;
        font-size: 13px;
        color: var(--theme-gray);
      }
      div p {
        color: #fff;
        font-size: 14px;
        line-height: 1.5em;
        margin-top: 15px;
      }
    }
    .poem-botton {
      width: 100%;
      height: 50px;
      padding-top: 5px;
      position: fixed;
      bottom: 0;
      text-align: center;
      background-image: url(../../assets/imgs/images/poemDeatilBg.png);
      button {
        display: inline-block;
        width: 50%;
        height: 40px;
        line-height: 1.5em;
        text-align: center;
        padding: 8px 15px;
        border-radius: 999px;
        background-color: #fff;
        color: var(--theme-green);
      }
    }
  }

  .album-info {
    width: 100vw;
    .album-info-header {
      width: 100%;
      height: 50%;
      .album-img {
        width: 100%;
        img {
          display: inline-block;
          width: 100%;
        }
      }
      // color:#d69472;
      .album-content {
        padding: 25px 15px;
        border-bottom: 5px solid var(--theme-french-gray);
        .album-header-title {
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: space-between;
          .album-title {
            h3 {
              font-size: 17px;
              color: #000;
              font-weight: 500;
            }
            p {
              margin-top: 5px;
              font-size: 13px;
              color: var(--theme-gray);
            }
          }
          .collect {
            width: 30px;
            img {
              width: 18px;
              height: 18px;
            }
          }
        }
        p {
          &:nth-of-type(1) {
            font-weight: bold;
            font-size: 18px;
            color: orangered;
            margin: 15px 0;
          }
          &:nth-of-type(2) {
            font-size: 15px;
            line-height: 1.5em;
            color: var(--theme-gray);
          }
        }
      }

      .detail-catalogue {
        padding: 0 15px;
        .text-pic::v-deep img {
          width: 100%;
        }
        .text-pic::v-deep p {
          width: 100%;
        }
        .text-pic::v-deep span {
          font-size: 12px;
        }
        .text-pic::v-deep section {
          width: 100%;
          overflow: hidden;
        }
        .catalogue-all {
          .catalogue-all-hearder {
            margin-top: 15px;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            .catalogue-all-hearder-title {
              flex: 1;
              display: flex;
              flex-direction: row;
              align-items: center;
              img {
                width: 25px;
                height: 25px;
                margin-right: 10px;
              }
              p {
                font-size: 15px;
                span {
                  font-size: 15px;
                  color: var(--theme-gray);
                }
              }
            }
            img {
              width: 15px;
              height: 15px;
            }
          }
          .catalogue-all-item-all {
            .catalogue-all-item {
              margin: 25px 0;
              display: flex;
              flex-direction: row;
              align-items: center;
              justify-content: space-between;
              .item-text {
                h3 {
                  padding-right: 15px;
                  font-size: 15px;
                  width: 100%;
                  display: -webkit-box;
                  overflow: hidden;
                  text-overflow: ellipsis;
                  -webkit-box-orient: vertical;
                  -webkit-line-clamp: 1;
                }
                p {
                  margin-top: 3px;
                  font-size: 13px;
                  color: var(--theme-gray);
                }
              }
              img {
                vertical-align: middle;
                width: 25px;
                height: 25px;
              }
              span {
                display: inline-block;
                vertical-align: middle;
                padding: 5px 8px;
                width: 50px;
                line-height: 10px;
                height: 20px;
                font-size: 12px;
                border-radius: 999px;
                text-align: center;
                border: 1px solid var(--theme-green);
                color: var(--theme-green);
              }
            }
          }
        }
      }
      .page-button {
        position: sticky;
        bottom: 0;
        left: 0;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        height: 50px;
        border-top: 1px solid var(--theme-french-gray);
        background-color: #fff;
        button {
          .line-through {
            margin-left: 5px;
            text-decoration: line-through;
          }
        }
      }
    }
  }

  .video-info {
    width: 100vw;
    .video {
      padding: 15px;
      width: 100%;
      border-bottom: 8px solid var(--theme-french-gray);
      .this-video {
        width: 100%;
        margin-bottom: 8px;
      }
      .video-header {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        width: 100%;
        h3 {
          flex: 1;
          margin-right: 20px;
          line-height: 1.5em;
          font-size: 20px;
        }
        img {
          width: 25px;
          height: 25px;
        }
      }
      .video-content {
        p {
          font-size: 15px;
          line-height: 1.5em;
          color: var(--theme-deep-gray);
          &:first-child {
            font-size: 13px;
            color: var(--theme-gray);
            margin: 10px 0 15px;
          }
        }
      }
    }
    .video-evaluate {
      padding: 20px 0;
      .video-evaluate-title {
        padding: 0 15px;
        font-size: 20px;
        font-family: fzqk;
      }
      .video-evaluate-content {
        .video-evaluate-content-item {
          width: 100%;
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          padding: 15px;
          .video-evaluate-left {
            flex: 0;
            width: 50px;
            height: 50px;
            margin-right: 5px;
            .left-img {
              vertical-align: top;
              width: 50px;
              height: 50px;
              overflow: hidden;
              border-radius: 999px;
              position: relative;
              img {
                width: 50px;
                height: 50px;
              }
              .video-vip {
                position: absolute;
                top: 30px;
                right: 5px;
                width: 20px;
                height: 20px;
              }
            }
          }
          .video-evaluate-center {
            flex: 1;
            h3 {
              font-size: 15px;
            }
            span {
              font-size: 12px;
              color: var(--theme-gray);
            }
            p {
              margin: 15px 0 10px;
              line-height: 1.5em;
              font-size: 15px;
            }
          }
          .video-evaluate-right {
            flex: 0;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: end;
            p {
              display: flex;
              flex-direction: row;
              justify-content: space-between;
              align-items: center;
              img {
                width: 25px;
                height: 25px;
                margin-right: 8px;
              }
              span {
                font-size: 14px;
                color: var(--theme-green);
              }
            }
