
<template>
  <div id="type2">
    <header>
      <img src="../../assets/imgs/images/pre.png" @click="goback" />
      <span>更多专辑</span>
    </header>
    <div class="type2-content">
      <div
        class="type2-item"
        v-for="item in allData"
        :key="item.id"
        @click="routerPage(item.id)"
      >
        <img :src="item.show_ing_url" />
        <h3>{{ item.album_name }}</h3>
        <p>
          <img src="../../assets/imgs/images/xsj.png" />
          <span>{{ item.view_count }}人阅读</span>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "TypeSection2",
  data() {
    return {
      allData: {},
    };
  },
  created() {
    this.$nextTick(() => {
      this.getAllData();
    });
  },
  methods: {
    goback() {
      this.$router.go(-1);
    },
    routerPage(id) {
      this.$router.push({
        name: "section",
        params: {
          type: 3,
          id,
        },
      });
    },
    async getAllData() {
      let data = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getChildAlbumList",
        data: {
          page: 1,
          limit: 12,
          str: "",
          freeType: 1,
        },
      });
      this.allData = data.data.data.data;
    },
  },
};
</script>

<style lang="scss" scoped>
#type2 {
  padding: 30px 0;
  header {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 50px;
    padding: 0 20px;
    background-color: #fff;
    overflow: hidden;
    margin-bottom: 10px;
    border-bottom: 1px solid var(--theme-french-gray);
    img {
      width: 10px;
    }
    span {
      width: 100%;
      text-align: center;
    }
  }
  .type2-content {
    margin-top: 25px;
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 15px;
    .type2-item {
      width: 48%;
      height: 230px;
      background-color: rgb(239, 239, 239, 0.8);
      border-radius: 10px;
      border: 10px solid rgb(239, 239, 239, 0.8);
      margin-bottom: 20px;
      img {
        width: 100%;
        height: 161px;
        border-radius: 5px;
      }
      h3 {
        width: 100%;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        font-size: 15px;
        margin: 8px;
      }
      p {
        img {
          width: 5px;
          height: 5px;
        }
        span {
          font-size: 14px;
          color: var(--theme-gray);
        }
      }
    }
  }
}
</style>