<template>
  <div id="type7">
    <header>
      <img src="../../assets/imgs/images/pre.png" @click="goback" />
      <van-search
        v-model="value"
        placeholder="搜索你喜欢的嘉宾"
        shape="round"
      />
    </header>
    <div class="banner">
      <van-swipe :autoplay="3000" indicator-color="var(--theme-gray)">
        <van-swipe-item v-for="(image, index) in banners" :key="index">
          <img v-lazy="image.image_url" />
        </van-swipe-item>
      </van-swipe>
    </div>
    <div class="poem-person">
      <h3>
        更多读诗人<span>(共{{ poemNum }}位)</span>
      </h3>
      <div class="poem-self">
        <div
          class="poem-item"
          v-for="item in allData"
          :key="item.gid"
          @click="routerDetailPage(item.gid)"
        >
          <img :src="item.img_new" />
          <div>
            <h3>{{ item.g_name }}</h3>
            <span>{{ item.title }}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "TypeSection7",
  data() {
    return {
      allData: [],
      banners: [],
      value: "",
      poemNum: 0,
    };
  },
  created() {
    this.$nextTick(() => {
      this.getBanners();
      this.getAllData();
    });
  },
  methods: {
    goback() {
      this.$router.go(-1);
    },
    async getAllData() {
      let data = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getGuestList",
        data: {
          page: 1,
          limit: 20,
          str: "",
        },
      });
      this.allData = data.data.data.data;
      this.poemNum = data.data.data.total;
    },
    async getBanners() {
      let data = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getBannerList",
        data: {
          position: 3,
        },
      });
      this.banners = data.data.data;
    },
    routerDetailPage(id) {
      this.$router.push({
        path: "/type8-item",
        query: {
          id,
        },
      });
    },
  },
};
</script>

<style lang="scss" scoped>
@font-face {
  font-family: "fzqk";
  src: url("../../assets/imgs/fzqk.TTF");
  font-family: "dcloud_iconfont";
  src: url("../../assets/fonts/dcloud_iconfont.ttf");
}
#type7 {
  padding: 30px 0;
  header {
    z-index: 999;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 0 20px;
    height: 50px;
    background-color: #fff;
    overflow: hidden;
    margin-bottom: 10px;
    border-bottom: 1px solid var(--theme-french-gray);
    img {
      width: 10px;
      // margin-left: 15px;
    }
    .van-search {
      flex: 1;
      width: 100%;
    }
  }
  .banner {
    margin-top: 40px;
    padding: 0px 20px;
    width: 100%;
    height: 190px;
    overflow: hidden;
    img {
      border-radius: 8px;
      width: 100%;
      height: 190px;
    }
  }
  .poem-person {
    padding: 20px;
    margin: 20px 0;
    h3 {
      font-size: 15px;
      font-family: fzqk;
      margin-bottom: 15px;
      span {
        margin-left: 5px;
        font-size: 14px;
        color: var(--theme-gray);
      }
    }
    .poem-self {
      .poem-item {
        font-size: 16px;
        margin: 10px 0;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        img {
          width: 50px;
          height: 50px;
          border-radius: 999px;
          border: 5px solid var(--theme-french-gray);
        }
        div {
          flex: 1;
          margin-left: 15px;
          h3 {
            font-size: 14px;
            margin: 0;
            font-weight: 500;
          }
          span {
            margin-top: 5px;
            font-size: 12px;
            color: var(--theme-gray);
          }
        }
      }
    }
  }
}
</style>