<template>
  <div id="record-section">
    <section class="content2">
      <h3 v-if="!(listData !== undefined) && listData !== null">
        {{ contentTitle2 }}
      </h3>
      <div
        class="content2-text"
        v-if="!(listData !== undefined) && listData !== null"
      >
        <div
          class="text-item"
          v-for="item in content2"
          :key="item.id"
          @click="routerPage(item)"
        >
          <div class="item-con">
            <div class="item-left">
              <h3>{{ item.title }}</h3>
              <p>{{ item.author_name }}</p>
              <p>{{ item.original }}</p>
            </div>
            <div class="item-right">
              <span>朗读</span>
              <span
                ><img src="../assets/imgs/images/fire.png" />{{
                  item.look_count
                }}人读过</span
              >
            </div>
          </div>
          <div class="item-bgc">
            <span>THE POEM FOR YOU</span>
            <span v-for="it in item.classify" :key="it.id">{{
              it.poem_classify_name
            }}</span>
          </div>
        </div>
      </div>
      <div class="content2-text" v-if="listData !== undefined">
        <div
          class="text-item"
          v-for="item in listData"
          :key="item.id"
          @click="routerPage(item)"
        >
          <div class="item-con">
            <div class="item-left">
              <h3>{{ item.title }}</h3>
              <p>{{ item.author_name }}</p>
              <p>{{ item.original }}</p>
            </div>
            <div class="item-right">
              <span>朗读</span>
              <span
                ><img src="../assets/imgs/images/fire.png" />{{
                  item.look_count
                }}人读过</span
              >
            </div>
          </div>
          <div class="item-bgc">
            <span>THE POEM FOR YOU</span>
            <span v-for="it in item.classify" :key="it.id">{{
              it.poem_classify_name
            }}</span>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  props: {
    listData: Array,
  },
  data() {
    return {
      contentTitle2: "",
      content2: [],
    };
  },
  created() {
    this.$nextTick(() => {
      this.getListData1();
      this.getHeaderData();
    });
  },
  methods: {
    async getHeaderData() {
      let data2 = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getReadBookTitle",
        data: {
          type: 9,
        },
      });
      this.contentTitle2 = data2.data.data;
    },
    async getListData1() {
      let data1 = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getPoemListApp",
        data: {
          page: 1,
          limit: 12,
          str: "",
        },
      });
      this.content2 = data1.data.data.data;
    },
    routerPage(item) {
      this.$router.push({
        path: "/record-poem",
        query: {
          item,
        },
      });
    },
  },
};
</script>

<style lang="scss" scoped>
#record-section {
  padding: 0 20px;
  .content2 {
    h3 {
      font-family: fzqk;
      font-size: 15px;
    }
    // padding: 1px;
    .content2-text {
      .text-item {
        margin: 10px 0;
        .item-con {
          margin-top: 13px;
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          .item-left {
            width: 70%;
            display: flex;
            flex-direction: column;
            h3 {
              font-size: 14px;
              padding: 0;
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
            }
            p {
              color: var(--theme-gray);
              display: -webkit-box;
              overflow: hidden;
              text-overflow: ellipsis;
              -webkit-box-orient: vertical;
              -webkit-line-clamp: 2;
              font-size: 13px;

              &:nth-last-child(2) {
                margin: 2px 0;
                font-size: 12px;
                color: var(--theme-gray);
              }
              &:last-child {
                line-height: 1.3em;
              }
            }
          }

          .item-right {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-evenly;
            span {
              font-size: 12px;
              color: var(--theme-gray);
              &:first-child {
                font-size: 12px;
                padding: 8px 12px;
                border-radius: 999px;
                border: 1px solid var(--theme-green);
                color: var(--theme-green);
              }
              img {
                width: 10px;
                height: 10px;
                margin-right: 5px;
              }
            }
          }
        }
        .item-bgc {
          padding: 5px 8px;
          width: 100%;
          font-size: 12px;
          color: var(--theme-gray);
          background-color: rgb(221 241 224);
          margin-top: 5px;
          margin-bottom: 15px;
          span {
            margin-right: 5px;
          }
        }
      }
    }
  }
}
</style>