<script>
// import SearchItem from "@/components/SearchItem.vue";
// import PoemBook from "@/components/PoemBook.vue";
export default {
  // components: { SearchItem, PoemBook },
  data() {
    return {
      inputvalue: "",
      trendingData: [],
      historySearchData: [],
      // searchListShow: true,
      // moreListShow: false,
      // searchList: [],
      // searchMoreThemeList: [],
      // type: -1,
      morePageDataToThis: this.$route.params.value,
    };
  },
  created() {
    this.$nextTick(() => {
      this.getTrendingData();
      this.morePageToThis();
    });
  },
  methods: {
    morePageToThis() {
      if (this.morePageDataToThis !== undefined) {
        this.inputvalue = this.morePageDataToThis;
        // this.searchListShow = false;
        // this.moreListShow = false;
        // this.getSearchData();
      }
    },
    async getTrendingData() {
      let data = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getSearchTip",
      });
      this.trendingData = data.data.data;
      if (localStorage.historySearchData != null) {
        this.historySearchData = JSON.parse(
          localStorage.getItem("historySearchData")
        );
      }
    },
    gohome() {
      this.$router.back(-1);
      // if (this.moreListShow && this.searchListShow) {
      //   this.moreListShow = false;
      //   this.searchListShow = false;
      // } else if (!this.moreListShow && !this.searchListShow) {
      //   this.searchListShow = true;
      // } else if (this.searchListShow && !this.moreListShow) {
      //   this.$router.push({
      //     path: "/",
      //   });
      // }
    },
    startSearch() {
      // this.searchListShow = false;
      if (this.historySearchData.indexOf(this.inputvalue) == -1) {
        this.historySearchData.unshift(this.inputvalue);
      } else {
        this.historySearchData = this.historySearchData.filter((item) => {
          if (item == this.inputvalue) {
            return false;
          } else {
            return true;
          }
        });
        this.historySearchData.unshift(this.inputvalue);
      }

      localStorage.setItem(
        "historySearchData",
        JSON.stringify(this.historySearchData)
      );
      if (localStorage.historySearchData != null) {
        this.historySearchData = JSON.parse(
          localStorage.getItem("historySearchData")
        );
      }
      // this.getSearchData();
      this.routertoSearchSection();
    },
    routertoSearchSection() {
      this.$router.push({
        path: "/search/search-section",
        query: {
          value: this.inputvalue,
        },
      });
    },
    // async getSearchData() {
    //   let data = await this.$axios({
    //     method: "post",
    //     url: "https://app.thepoemforyou.com/api/v1/SearchAll",
    //     data: {
    //       str: this.inputvalue,
    //     },
    //     header: {
    //       "Content-Type": "application/x-www-form-urlencoded",
    //     },
    //   });
    //   this.searchList = data.data.data;
    // },
    // setSearchListShow() {
    //   this.searchListShow = true;
    // },

    clickSearchItem(e) {
      this.inputvalue = e.target.innerText;
      this.routertoSearchSection();
      // this.startSearch();
    },

    // searchMoreList(num) {
    //   this.searchListShow = true;
    //   this.moreListShow = true;
