<template>
  <div id="deblock-section">
    <div
      class="deblock-item"
      v-for="item in tabBar"
      :key="item.id"
      @click="routerPage(item.id)"
    >
      <div class="deblock-img"><img :src="item.calssify_img" /></div>
      <span>{{ item.classify_name }}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: "DeblockSection",
  props: {
    tabBar: Array,
  },
  data() {
    return {
      moduleObj: [],
    };
  },
  methods: {
    routerPage(id) {
      this.getData(id);
    },
    async getData(id) {
      let data = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getTabBarAlbum",
        data: {
          type: id,
          limit: 10,
          page: 1,
        },
      });
      this.moduleObj = data.data.data.data;
      this.$router.push({
        name:"StudySection",
        params:{
          obj:this.moduleObj
        }
      })
    },
  },
};
</script>

<style lang="scss" scoped>
#deblock-section {
  margin-top: 10px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  .deblock-item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: max-content;
    padding: 10px;
    font-size: 12px;
    .deblock-img {
      overflow: hidden;
      border-radius: 999px;
      img {
        width: 40px;
        height: 40px;
      }
    }
    span {
      text-align: center;
    }
  }
}
</style>