<template>
  <div class="module-type11">
    <section>
      <div class="member">
        <div
          v-show="!showItem"
          class="member-book"
          v-for="it in moduleObj.albumList"
          :key="it.programId"
          @click="routerPage(it.programId)"
        >
          <div class="member-book-img">
            <img :src="it.programImg" />
          </div>
          <p>{{ it.programTit }}</p>
          <p>￥{{ it.price / 100 }}</p>
        </div>
        <div
          v-show="showItem"
          class="member-book"
          v-for="it in moduleObj"
          :key="it.id"
        >
          <div class="member-book-img">
            <img :src="it.image_url" />
          </div>
          <p>{{ it.name }}</p>
          <p>￥{{ it.line_price / 100 }}</p>
        </div>
      </div>
    </section>
  </div>
</template>
<script>
export default {
  name: "moduleType9",
  props: {
    moduleObj: [Object, Array],
  },
  data() {
    return {
      showItem: false,
    };
  },
  created() {
    this.$nextTick(() => {
      this.getData();
    });
  },
  methods: {
    getData() {
      this.showItem = this.moduleObj.albumList == undefined ? true : false;
    },
    routerPage(id) {
      this.$router.push({
        path: "/module11-router",
        query: {
          id: id,
        },
      });
    },
  },
};
</script>
<style lang="scss" scoped>
@font-face {
  font-family: "fzqk";
  src: url("../assets/imgs/fzqk.TTF");
  font-family: "dcloud_iconfont";
  src: url("../assets/fonts/dcloud_iconfont.ttf");
}
.module-type11 {
  margin: 0 20px;
  padding-bottom: 15px;
  border-bottom: 1px solid rgb(237, 237, 237);

  section {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    overflow: scroll;
    &::-webkit-scrollbar {
      display: none;
    }
    .member {
      display: flex;
      flex-direction: row;
      align-items: center;
      width: 1000%;
      .member-book {
        height: 128px;
        width: 100px;
        margin-right: 18px;
        border-radius: 5px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        background-color: var(--theme-french-green);
        .member-book-img {
          display: flex;
          justify-content: center;
          align-items: center;
          img {
            width: 100%;
          }
        }
        p {
          height: 15px;
          padding-bottom: 5px;
          font-size: 20px;
          transform: scale(0.5);
          padding: 5px 0;
          &:last-child {
            color: var(--theme-green);
          }
        }
      }
    }
  }
}
</style>