<template>
  <div class="module-type2">
    <div class="select-book">
      <div
        class="select-book-item"
        v-for="it in moduleObj.albumList"
        :key="it.programId"
        @click="routerPage(it)"
        v-show="!showItem"
      >
        <div class="select-book-img">
          <img :src="it.programImg" />
        </div>
        <div class="select-book-text">
          <h3>{{ it.programTit }}</h3>
          <p>{{ it.remark }}</p>
          <p>
            <span>{{ it.programNum }}阅读</span>
            <span>{{ it.price / 100 }}元</span>
          </p>
        </div>
      </div>
      <div
        class="select-book-item"
        v-for="it in moduleObj"
        :key="it.id"
        v-show="showItem"
        @click="routerPage(it)"
      >
        <div class="select-book-img">
          <img :src="it.image" />
        </div>
        <div class="select-book-text">
          <h3>{{ it.name }}</h3>
          <p>{{ it.remark }}</p>
          <p>
            <span>{{ it.view_count }}阅读</span>
            <span
              ><span class="line-through">{{ it.price / 100 }}元</span>
              会员免费</span
            >
          </p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "ModuleType2",
  props: {
    moduleObj: [Object, Array],
  },
  created() {
    this.getAllData();
  },
  data() {
    return {
      showItem: false,
    };
  },
  methods: {
    getAllData() {
      this.showItem = this.moduleObj.albumList == undefined ? true : false;
    },
    routerPage(item) {
      this.$router.push({
        name: "section",
        params: {
          type: 3,
          id: item.programId !== undefined ? item.programId : item.id,
          value: "",
        },
      });
    },
  },
};
</script>
<style lang="scss" scoped>
@font-face {
  font-family: "fzqk";
  src: url("../assets/imgs/fzqk.TTF");
  font-family: "dcloud_iconfont";
  src: url("../assets/fonts/dcloud_iconfont.ttf");
}
.module-type2 {
  margin: 0 20px;
  padding-bottom: 15px;
  // &:not(:last-child) {
  border-bottom: 1px solid var(--theme-line-bottom);
  // }

  .select-book {
    width: 100%;
    .select-book-item {
      width: 100%;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 20px;
      .select-book-img {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 40%;
        height: 80px;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .select-book-text {
        width: 70%;
        padding-left: 15px;
        h3 {
          width: 90%;
          font-size: 14px;
          font-weight: 500;
          overflow: hidden;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 1;
        }
        p {
          display: flex;
          padding-right: 15px;
          color: var(--theme-deep-gray);
          &:nth-of-type(1) {
            font-size: 12px;
            margin: 8px 0 10px;
            width: 90%;
            height: 13px;
            overflow: hidden;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 1;
          }
          &:nth-last-of-type(1) {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            font-size: 12px;
            span {
              color: var(--theme-gray);
              font-family: dcloud_iconfont;
              &:last-child {
                color: orangered;
              }
              .line-through {
                text-decoration: line-through;
              }
            }
          }
        }
      }
    }
  }
}
</style>