<template>
  <div class="search-item" @click="routerDetail">
    <div class="item-img" :class="{ circle: readingBook }">
      <img :src="pic" />
      <img
        class="start"
        v-if="readingperson"
        src="../assets/imgs/images/songBegin.png"
      />
    </div>
    <div class="content">
      <h3>
        <span>{{ title }}</span>
        <img v-show="freeType == 2" src="../assets/imgs/images/pay.gif" />
        <img v-show="isVipFree == 1" src="../assets/imgs/images/vipFree.gif" />
      </h3>
      <p v-if="!readingperson">{{ SecondTitle }}</p>
      <p v-if="readingperson">阅读{{ SecondTitle }}次</p>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    pic: String,
    title: String,
    SecondTitle: [String, Number],
    freeType: [Number, Boolean],
    readingBook: Boolean,
    type: Number,
    isVipFree: [Number, Boolean],
    value: String,
    id: Number,
    readingperson: Boolean,
    canClick: {
      typeof: Boolean,
      default: true,
    },
    listeningReadingBook: {
      typeof: Boolean,
      default: false,
    },
  },
  name: "searchItem",
  methods: {
    routerDetail() {
      this.getClickLocalData();
      if (this.canClick) {
        if (this.type == 4 || this.type == 1) {
          this.$router.push({
            path: "/detailMusic",
            query: {
              type: this.type,
              value: this.value,
              id: this.id,
            },
          });
        } else if (this.type == 6) {
          this.$router.push({
            path: "/type4-item",
            query: {
              id: this.id,
            },
          });
        } else {
          this.$router.push({
            name: "section",
            params: {
              type: this.type,
              value: this.value,
              id: this.id,
            },
          });
        }
      }
    },
    getClickLocalData() {
      let obj = {
        type: this.type,
        value: this.value,
        id: this.id,
      };
      if (localStorage.clickItemData == undefined) {
        localStorage.setItem("clickItemData", JSON.stringify(obj));
      } else {
        localStorage.setItem("clickItemData", JSON.stringify(obj));
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.search-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin: 0 20px;
  padding: 10px 0;
  &:not(:last-child) {
    border-bottom: 1px solid var(--theme-title-bottom);
  }
  .item-img {
    position: relative;
    width: 50px;
    height: 50px;
    img {
      width: 50px;
      height: 50px;
    }
    .start {
      position: absolute;
      top: 20%;
      left: 20%;
      width: 25px;
      height: 25px;
      transform: translateX(50%) translateY(50%);
    }
  }
  .circle {
    overflow: hidden;
    border-radius: 999px;
  }
  .content {
    margin-left: 15px;
    flex: 1;
    h3 {
      display: flex;
      flex-direction: row;
      justify-content: center;
      width: 100%;
      align-items: center;
      span {
        flex: 1;
        display: -webkit-box;
        overflow: hidden;
        text-overflow: ellipsis;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 1;
        font-size: 14px;
        font-weight: 500;
      }
      img {
        flex: 0;
        height: 15px;
        margin: 0 5px;
      }
    }
    p {
      margin-top: 5px;
      font-size: 13px;
      color: var(--theme-gray);
    }
  }
}
</style>