<template>
  <div class="calendar">
    <header>
      <img src="../../assets/imgs/images/pre.png" @click="goback" />
      <p>日签</p>
    </header>
    <div class="calender-content">
      <p>
        <span>{{ year }}年{{ month }}月</span>
        <span @click="getTodayPic">今日</span>
      </p>
      <p>
        <VueCalendarWeek
          class="calendar-item"
          v-model="dateValue"
          :threshold="render"
          @click="clickCalendar"
        />
      </p>
    </div>
    <div class="calender-img">
      <img :src="dateUrl" />
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      dateUrl: "",
      dateValue: new Date(),
      year: new Date().getFullYear(),
      month: new Date().getMonth() + 1,
      day: new Date().getDate(),
      current: new Date(),
      render: 50,
    };
  },
  created() {
    this.$nextTick(()=>{
      this.getDayData();
    })
  },
  methods: {
    goback() {
      this.$router.back(-1);
    },
    async getDayData() {
      let str = this.year + "-" + this.month + "-" + this.day;
      let data = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getNewImageByDate",
        data: {
          date: str,
        },
      });
      this.dateUrl = data.data.data.image_url;
    },
    clickCalendar() {
      this.day = this.dateValue.getDate();
      this.month = this.dateValue.getMonth();
      this.year = this.dateValue.getFullYear();
      this.getDayData();
    },
    getTodayPic() {
      this.year = this.current.getFullYear();
      this.month = this.current.getMonth();
      this.day = this.current.getDate();
      this.getDayData();
    },
  },
};
</script>
<style lang="scss" scoped>
.calendar {
  header {
    position: sticky;
    top: 0;
    left: 0;
    padding: 10px 15px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-color: #fff;
    z-index: 999;
    img {
      width: 15px;
    }
    p {
      width: 100%;
      font-size: 18px;
      text-align: center;
    }
  }
  .calender-content {
    position: sticky;
    top: 0;
    left: 0;
    background-color: #fff;
    p {
      display: flex;
      flex-direction: row;
      justify-content: space-evenly;
      align-items: center;
      margin: 10px 0;
      width: 100%;
      &:first-child {
        position: relative;
        justify-content: space-between;
        margin-top: 5px;
        span {
          &:first-child {
            width: 100%;
            text-align: center;
          }
          &:last-child {
            position: absolute;
            right: 15px;
            color: var(--theme-green);
          }
        }
      }
      span {
        padding: 5px 8px;
      }
      .today {
        color: #fff;
        border-radius: 999px;
        background-color: var(--theme-green);
      }
      &:nth-of-type(2) {
        .calendar-item ::v-deep .calendars-slider-week .day.active span {
          background-color: var(--theme-green);
        }
      }
    }
    .now {
      position: relative;
      width: 100%;
      height: 10px;
      margin-top: 8px;
      &::before {
        position: absolute;
        bottom: 5px;
        left: 47%;
        display: inline-block;
        content: "";
        // width: 10px;
        // height: 10px;

        border: 12px solid var(--theme-green);
        border-bottom: 0px;
        border-left: 12px solid transparent;
        border-right: 12px solid transparent;
      }
    }
  }
  .calender-img {
    padding: 15px 20px;
    background-color: rgb(237, 236, 236);
    img {
      width: 100%;
    }
  }
}
</style>