<template>
  <div id="study">
    <header>
      <van-search
        placeholder="搜索你喜欢的嘉宾, 课程,主题"
        shape="round"
        v-model="inputValue"
        input-align="center"
        @click="inputPage"
      />
    </header>
    <div class="banner">
      <van-swipe
        :autoplay="3000"
        :height="190"
        indicator-color="var(--theme-gray)"
      >
        <van-swipe-item
          v-for="(image, index) in bannerImgs"
          :key="index"
          @click="routerPage(image)"
        >
          <img v-lazy="image.image_url" />
        </van-swipe-item>
      </van-swipe>
    </div>
    <div class="deblock">
      <DeblockSection :tabBar="sectionList.tabBar"></DeblockSection>
    </div>
    <section>
      <div
        class="seciton-item"
        v-for="item in sectionList.album"
        :key="item.moduleTitle"
      >
        <ModuleTypeHeader :moduleObjItem="item"></ModuleTypeHeader>
        <component
          :is="moduleType[item.moduleType - 1]"
          :moduleObj="item"
        ></component>
      </div>
    </section>
  </div>
</template>
<script>
import ModuleTypeHeader from "@/components/ModuleTypeHeader.vue";
import DeblockSection from "@/components/DeblockSection.vue";
export default {
  components: {
    ModuleType1: () => import("@/components/ModuleType1.vue"),
    ModuleType2: () => import("@/components/ModuleType2.vue"),
    ModuleType3: () => import("@/components/ModuleType3.vue"),
    ModuleType6: () => import("@/components/ModuleType6.vue"),
    ModuleType7: () => import("@/components/ModuleType7.vue"),
    ModuleType8: () => import("@/components/ModuleType8.vue"),
    ModuleType9: () => import("@/components/ModuleType9.vue"),
    ModuleType11: () => import("@/components/ModuleType11.vue"),
    ModuleTypeHeader,
    DeblockSection,
  },
  name: "StudyView",
  data() {
    return {
      bannerImgs: [],
      sectionList: [],
      inputValue: "",
      moduleType: [
        "ModuleType1",
        "ModuleType2",
        "ModuleType3",
        "",
        "",
        "ModuleType6",
        "ModuleType7",
        "ModuleType8",
        "ModuleType9",
        "",
        "ModuleType11",
      ],
    };
  },
  created() {
    this.$nextTick(() => {
      this.getBannerData();
      this.getSectionListData();
    });
  },
  methods: {
    async getBannerData() {
      let data = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getBannerList",
        data: {
          position: 2,
        },
      });
      this.bannerImgs = data.data.data;
    },
    async getSectionListData() {
      let data = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getLearnAlbumList",
      });
      this.sectionList = data.data.data;
    },
    inputPage() {
      this.$router.push({
        path: "study-search",
        query: {
          value: this.inputValue,
        },
      });
    },
    routerPage(item) {
      let id = Number(item.link_url.split("id=")[1]);
      this.$router.push({
        name: "section",
        params: {
          id,
          type: 3,
        },
      });
    },
  },
};
</script>
<style lang="scss" scoped>
#study {
  header {
    padding: 5px;
    font-size: 12px;
  }
  .banner {
    padding: 0px 20px;
    width: 100%;
    height: 190px;
    overflow: hidden;
    border-radius: 10px;
    img {
      width: 100%;
    }
  }
  .deblock {
    padding: 0 20px;
  }
  section {
    margin-bottom: 80px;
    .seciton-item {
      margin-top: 10px;
    }
  }
}
</style>