-->
<template>
  <div class="module-type-header">
    <header>
      <h3>{{ moduleObjItem.moduleTitle }}</h3>
      <span @click="moreList" v-if="moduleObjItem.moreType !== 0">
        查看更多
        <img src="../assets/imgs/images/next.png" />
      </span>
    </header>
  </div>
</template>
<script>
export default {
  name: "moduleObjTitle",
  props: {
    moduleObjItem: Object,
  },
  methods: {
    moreList() {
      if (this.moduleObjItem.type == 2) {
        if (this.moduleObjItem.moreType == 2) {
          this.$router.push({
            name: "type22",
          });
        } else {
          this.$router.push({
            name: "type2",
          });
        }
      } else if (this.moduleObjItem.type == 7) {
        this.$router.push({
          name: "type7",
        });
      } else if (this.moduleObjItem.type == 3) {
        this.$router.push({
          name: "type3",
        });
      } else if (this.moduleObjItem.type == 4) {
        this.$router.push({
          name: "type4",
        });
      } else if (this.moduleObjItem.type == 6) {
        this.$router.push({
          name: "present",
        });
      } else if (this.moduleObjItem.type == 9) {
        this.$router.push({
          name: "type9",
        });
      }
    },
  },
};
</script>
<style lang="scss" scoped>
@font-face {
  font-family: "fzqk";
  src: url("../assets/imgs/fzqk.TTF");
  font-family: "dcloud_iconfont";
  src: url("../assets/fonts/dcloud_iconfont.ttf");
}
.module-type-header {
  margin-top: 5px;
  padding: 10px 20px 0;
  header {
    display: flex;
    flex-direction: row;
    align-items: baseline;
    justify-content: space-between;
    margin-bottom: 10px;
    h3 {
      font-size: 15px;
      font-family: fzqk;
    }
    span {
      font-size: 12px;
      color: var(--theme-gray);
      img {
        width:5px;
        height: 10px;
      }
    }
  }
}
</style>