<template>
  <div id="music">
    <div class="music-details">
      <header>
        <van-nav-bar
          title="聆听诗意"
          class="nav-content"
          @click-left="clickLeft"
          @click-right="clickRight"
        >
          <template #left>
            <img src="../../assets/imgs/images/pre.png" />
          </template>
          <template #right>
            <img src="../../assets/imgs/images/share.png" />
            <van-share-sheet
              v-model="showShare"
              :options="options"
              get-container="#music"
            />
          </template>
        </van-nav-bar>
      </header>
      <div class="main-music" ref="musicBgc">
        <div class="bgc-blur">
          <div class="music-img-box">
            <img v-show="!otherTypeMusic" :src="musicData.str_img_url" />
            <div v-show="otherTypeMusic" class="other-music-box">
              <img :src="musicData.str_img_url" />
            </div>
          </div>
          <p>{{ musicData.str_program_title }}</p>
          <div class="audio-components">
            <audio
              :src="musicData.str_audio_url"
              autoplay
              ref="musicAudio"
            ></audio>
            <p>
              {{ musicCurrentTime }}
              <progress
                ref="progress"
                type="range"
                min="0"
                max="100"
                value="0"
                id="progress"
                @click="getProgressValue"
              ></progress>
              {{ musicAllTime }}
            </p>
            <p class="music-controls">
              <img
                src="../../assets/imgs/images/program/sxxh.png"
                @click="musicReload"
              />
              <img
                src="../../assets/imgs/images/program/sq.png"
                @click="prevMusicData"
              />
              <span class="play" @click="setMusicIsPlay"></span>
              <img
                src="../../assets/imgs/images/program/xq.png"
                @click="nextMusicData"
              />
              <img src="../../assets/imgs/images/program/lb.png" />
            </p>
            <p class="music-share">
              <img src="../../assets/imgs/images/program/sc.png" />
              <img src="../../assets/imgs/images/program/ds.png" />
              <img src="../../assets/imgs/images/program/xz.png" />
              <img src="../../assets/imgs/images/program/pl.png" />
            </p>
          </div>
        </div>
      </div>

      <div v-show="otherTypeMusic" class="other-content">
        <div class="re-bgc" v-show="isRecommendAlbum">
          <div class="recommend_book">
            <img :src="recommendAlbum.show_ing_url" />
            <div class="re-book">
              <div>
                <h3>{{ recommendAlbum.album_name }}</h3>
                <p>{{ recommendAlbum.remark }}</p>
              </div>
              <p>{{ recommendAlbum.view_count }}人阅读</p>
            </div>
          </div>
        </div>
        <div class="music-author">
          <div
            class="author-item"
            v-for="item in musicData.str_readpeam"
            :key="item.gid"
          >
            <div class="author-pic">
              <img :src="item.img_new" />
            </div>
            <div class="author-play">
              <p>{{ item.g_name }}</p>
              <span>{{ item.title }}</span>
            </div>
          </div>
        </div>
        <div class="music-poem">
          <p>{{ musicData.str_peam_title }}</p>
          <p>{{ musicData.str_autor_info }}</p>
          <p v-for="(item, i) in musicPoem" :key="i">{{ item }}</p>
        </div>
        <div class="poem-share">
          <h3>- 诗享 -</h3>
          <p>Poetry Review</p>
          <div>
            <p v-for="(item, i) in poemPoetryEnjoy" :key="i">{{ item }}</p>
          </div>
        </div>
        <div class="pic-share">
          <h3>-图享-</h3>
          <p>Cover</p>
          <div>
            <img :src="musicData.str_picture_url" />
            <span>{{ musicData.str_picture_enjoy }}</span>
          </div>
        </div>
        <div class="happy-share">
          <h3>-乐享-</h3>
          <p>Soundtrack</p>
          <div>{{ musicData.str_music_enjoy }}</div>
        </div>
      </div>
      <div v-show="!otherTypeMusic" class="content" v-html="text"></div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      musicData: [],
      musicId: 4888,
      musicPlayId: [],
      musicPlayLenght: -1,
      text: "",
      musicIsPlay: true,
      showShare: false,
      otherTypeMusic: false,
      musicPoem: [], //
      recommendAlbum: {}, //
      isRecommendAlbum: false,
      poemPoetryEnjoy: [], //
      timer: "",
      options: [
        { name: "微信", icon: "wechat" },
        { name: "朋友圈", icon: "wechat-moments" },
        { name: "QQ", icon: "qq" },
      ],
      musicAllTime: "00:00:00",
      initAllTime: 0,
      musicCurrentTime: "00:000:00",
    };
  },
  created() {
    this.getMuiscData();
    this.getMusicPalyId();
  },
  mounted() {
    this.muiscPalyData();
  },
  methods: {
    async getMuiscData(id) {
      if (!isNaN(this.$route.query.id ?? NaN)) {
        id = this.$route.query.id;
        this.$route.query.id = NaN;
        this.otherTypeMusic = true;
      }
      let data = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getProgramInfo",
        data: {
          id: id ?? this.musicId,
        },
      });
      this.musicData = data.data.data;
      this.musicPoem =
        this.musicData.str_peam_content !== null
          ? this.musicData.str_peam_content.split("\n")
          : "";
      if (this.musicData.recommend_album !== null) {
        this.recommendAlbum = this.musicData.recommend_album;
        this.isRecommendAlbum = true;
      } else {
        this.isRecommendAlbum = false;
      }
      this.poemPoetryEnjoy =
        this.musicData.str_poetry_enjoy !== null
          ? this.musicData.str_poetry_enjoy.split("\n")
          : "";
      if (id !== undefined) {
        this.$refs.musicBgc.style.backgroundImage = `url(${this.musicData.str_img_url})`;
      } else {
        this.$refs.musicBgc.style.backgroundImage = "none";
      }
      // 字符串解析
      if (!this.otherTypeMusic) {
        this.text = this.musicData.rich_content.content;
        let index = this.text.indexOf('<img class="rich_pages"');
        let index1 = this.text.indexOf("/>");
        let t = this.text.substring(index, index1);
        let index2 = t.indexOf("width:");
        let index3 = t.indexOf("px", index2);
        let text1 = t.substring(index2, index3);
        let rep = t.replace(text1, "width:330");
        this.text = this.text.replace(t, rep);
      }
    },
    clickLeft() {
      this.$router.back(-1);
    },
    clickRight() {
      this.showShare = true;
    },
    muiscPalyData() {
      this.$nextTick(() => {
        this.timer = setInterval(() => {
          let duration = -1;
          duration = this.$refs.musicAudio.duration;
          this.musicAllTime = duration;
          if (duration != -1 && !isNaN(duration)) {
            clearInterval(this.timer);
            this.initAllTime = duration;
            this.musicAllTime = this.getMusicFormatTime(this.musicAllTime);
            let self = this;
            if (this.$refs.musicAudio !== undefined) {
              this.$refs.musicAudio.ontimeupdate = function () {
                self.musicCurrentTime = this.currentTime;
                let progressValue = (this.currentTime / this.duration) * 100;
                if (
                  !isNaN(progressValue) &&
                  self.$refs.progress !== undefined
                ) {
                  self.$refs.progress.value =
                    (this.currentTime / this.duration) * 100;
                }
                self.musicCurrentTime = self.getMusicFormatTime(
                  self.musicCurrentTime
                );
              };
            }
          }
        }, 2000);
      });
    },
    getProgressValue(e) {
      this.$refs.musicAudio.currentTime =
        (e.offsetX / e.target.offsetWidth) * this.$refs.musicAudio.duration;
      this.$refs.progress.value = (e.offsetX / e.target.offsetWidth) * 100;
    },
    getMusicFormatTime(str) {
      let hours = parseInt(str / 60 / 60);
      hours = hours > 10 ? hours : "0" + hours;
      let minutes = parseInt(str / 60);
      minutes = minutes > 10 ? minutes : "0" + minutes;
      let seconds = parseInt(str % 60);
      seconds = seconds > 10 ? seconds : "0" + seconds;
      str = `${hours}:${minutes}:${seconds}`;
      return str;
    },
    setMusicIsPlay(e) {
      this.musicIsPlay = !this.musicIsPlay;
      if (this.musicIsPlay) {
        e.target.className = "play";
        this.$refs.musicAudio.play();
      } else {
        e.target.className = "pause";
        this.$refs.musicAudio.pause();
      }
    },
    musicReload() {
      this.$refs.musicAudio.load();
    },
    async getMusicPalyId() {
      let data = await this.$axios({
        url: "https://app.thepoemforyou.com/api/v1/getNewsProgramNew",
      });
      data.data.data.map((item) => {
        this.musicPlayId.push(item.id);
      });
    },
    prevMusicData() {
      if (
        this.musicPlayLenght < this.musicPlayId.length - 1 &&
        this.musicPlayLenght >= -1
      ) {
        this.musicPlayLenght++;
        this.otherTypeMusic = true;
        let id = this.musicPlayId[this.musicPlayLenght];
        this.getMuiscData(id);
      } else {
        this.$toast("没有更多了");
      }
    },
    nextMusicData() {
      if (
        this.musicPlayLenght >= 0 &&
        this.musicPlayLenght < this.musicPlayId.length
      ) {
        this.musicPlayLenght--;
        if (this.musicPlayLenght < 0) {
          this.otherTypeMusic = false;
          this.getMuiscData();
        } else {
          let id = this.musicPlayId[this.musicPlayLenght];
          this.getMuiscData(id);
        }
      } else {
        this.$toast("没有更多了");
      }
    },
  },
  beforeDestroy() {
    this.$refs.musicAudio.pause();
    clearInterval(this.timer);
  },
};
</script>

<style lang="scss" scoped>
@font-face {
  font-family: "fzqk";
  src: url("../../assets/imgs/fzqk.TTF");
  font-family: "dcloud_iconfont";
  src: url("../../assets/fonts/dcloud_iconfont.ttf");
}
#music {
  width: 100vw;
  .music-details {
    width: 100%;
    header {
      width: 100%;
      position: fixed;
      top: 0;
      left: 0;
      z-index: 1;
      .nav-content {
        .van-nav-bar__left img {
          width: 10px;
        }
        .van-nav-bar__right img {
          width: 30px;
        }
      }
    }
    .main-music {
      z-index: 0;
      position: relative;
      width: 100vw;
      background-size: cover;
      .bgc-blur {
        background-color: rgba(255, 255, 255, 0.7);
        .music-img-box {
          width: 100%;
          height: 280px;
          img {
            width: 100%;
            height: 280px;
          }
        }
      }
      .other-music-box {
        position: absolute;
        top: 15%;
        left: 50%;
        transform: translateX(-50%);
        width: 150px;
        height: 150px;
        border-radius: 999px;
        overflow: hidden;
        border: 5px solid var(--theme-gray);
        img {
          height: 150px;
          width: 150px;
          border-radius: 999px;
          overflow: hidden;
        }
      }
    }
    p {
      text-align: center;
      margin: 15px 15px 20px;
    }
    .audio-components {
      z-index: 3;
      width: 100vw;
      p {
        font-size: 12px;
        text-align: center;
        #progress {
          display: inline-block;
          width: 60%;
          height: 3px;
          border-radius: 999px;
          background: var(--theme-gray);
          color: var(--theme-green);
        }
        #progress::-webkit-progress-bar {
          background-color: var(--theme-gray);
        }
        #progress::-webkit-progress-value {
          background-color: var(--theme-green);
        }
      }
      .music-controls {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-evenly;
        height: 50px;
        img {
          width: 20px;
        }
        .play {
          width: 50px;
          height: 50px;
          background-image: url(../../assets/imgs/images/program/zt_03.png);
          background-size: contain;
        }
        .pause {
          width: 50px;
          height: 50px;
          background-image: url(../../assets/imgs/images/program/bf.png);
          background-size: contain;
        }
      }
      .music-share {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        height: 50px;
        img {
          width: 20px;
          margin: 0 20px;
        }
      }
    }

    .other-content {
      width: 100vw;
      height: 150px;
      .re-bgc {
        width: 100%;
        padding: 20px 15px 25px;
        border-bottom: 1px solid var(--theme-gray);
        .recommend_book {
          width: 100%;
          box-shadow: 0px 0px 8px var(--theme-gray);
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          padding: 10px;
          img {
            width: 45%;
          }
          .re-book {
            width: 55%;
            margin-left: 15px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: start;
            div {
              width: 80%;
              h3 {
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                font-weight: bold;
                font-size: 18px;
              }
              p {
                margin-top: 5px;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                color: var(--theme-gray);
                font-size: 15px;
              }
            }
            p {
              margin: 0;
              padding: 0;
              color: var(--theme-deep-gray);
              font-size: 18px;
            }
          }
        }
      }
      .music-author {
        margin-top: 10px;
        .author-item {
          margin: 20px 15px;
          display: flex;
          flex-direction: row;
          align-items: center;
          .author-pic {
            width: 50px;
            height: 50px;
            border-radius: 999px;
            overflow: hidden;
            img {
              width: 50px;
              height: 50px;
            }
          }
          .author-play {
            margin-left: 15px;
            p {
              margin: 0;
              padding: 0;
              text-align: left;
              font-size: 15px;
            }
            span {
              margin-top: 5px;
              font-size: 12px;
              color: var(--theme-gray);
            }
          }
        }
      }
      .music-poem {
        padding: 15px;
        p {
          text-align: left;
          font-family: fzqk;
          &:nth-of-type(2) {
            font-size: 13px;
            font-family: dcloud_iconfont;
            color: var(--theme-gray);
          }
          &:nth-of-type(3) {
            font-size: 15px;
            line-height: 1.5em;
          }
        }
      }
      .poem-share,
      .pic-share,
      .happy-share {
        border-top: 10px solid var(--theme-french-gray);
        padding: 15px;
        h3,
        p {
          text-align: center;
          font-size: 20px;
        }
        p {
          color: var(--theme-green);
        }
      }

      .poem-share {
        div p {
          margin-top: 25px;
          padding: 0;
          font-size: 17px;
          text-align: left;
          line-height: 1.5em;
          color: var(--theme-gray);
        }
      }
      .pic-share {
        div img {
          width: 100%;
        }
      }
    }
    .content {
      padding: 15px;
      border-top: 4px solid var(--theme-french-gray);
      width: 100%;
      overflow: hidden;
    }
  }
}
</style>
