<template>
  <div>
    <div class="tabbars" ref="tabbars">
      <router-link to="/detailMusic" v-if="!$route.meta.showTabbarMusic">
        <div class="music" v-show="musicItemShow" @click="musicDataRouter">
          <img :src="musicData.str_img_url" />
          <p>{{ musicData.str_program_title }}</p>
          <span class="play" v-if="picIsPlay"></span>
          <span class="pause" v-if="!picIsPlay"></span>
        </div>
      </router-link>
      <div class="router" v-if="!$route.meta.showTabbar">
        <router-link to="/">
          <div @click="musicItemIsShow(1)">
            <i></i>
            首页
          </div>
        </router-link>
        <router-link to="/study">
          <div @click="musicItemIsShow(1)">
            <i></i>
            书房
          </div>
        </router-link>
        <router-link to="/record">
          <div @click="musicItemIsShow(1)">
            <i></i>
          </div>
        </router-link>
        <router-link to="/present">
          <div @click="musicItemIsShow(0)"><i></i> 礼物</div>
        </router-link>
        <router-link to="/my">
          <div @click="musicItemIsShow(1)">
            <i></i>
            我的
          </div>
        </router-link>
      </div>
    </div>
    <router-view
      @send-tabbar-play="sendApp"
      @send-tabbar-load="sendAppLoad"
      @send-Tabbar-progress="setAppProgress"
      @send-tabbar-music-id="getMusic"
      @collect-to-play-id="getMusic"
      :currentTime="currentTime"
      :duration="duration"
      :musicId="musicId"
      :picIsPlay="picIsPlay"
    ></router-view>
  </div>
</template>
<script>
export default {
  props: {
    currentTime: Number,
    duration: Number,
    picIsPlay: Boolean,
    appMusicId: [Number, String],
  },
  data() {
    return {
      musicData: [],
      musicItemShow: true,
      musicId: 4888,
    };
  },
  created() {
    this.$nextTick(() => {
      this.getMusic();
    });
  },
  watch: {
    appMusicId(newId) {
      this.musicId = newId;
      this.getMusic(newId);
      return newId;
    },
  },
  methods: {
    async getMusic(id) {
      if (id !== undefined) {
        this.musicId = Number(id);
        this.musicDataRouter(id);
      }
      let url = "";
      if (
        this.musicId.toString().length == 6 ||
        (id !== undefined && id.toString().length == 6)
      ) {
        url = "https://app.thepoemforyou.com/api/v1/getUserProgramInfo";
      } else {
        url = "https://app.thepoemforyou.com/api/v1/getProgramInfo";
      }
      let data = await this.$axios({
        method: "post",
        url: url,
        data: {
          id: this.musicId,
        },
      });
      this.musicData = data.data.data;
      this.src = this.musicData.str_audio_url;
    },
    musicItemIsShow(num) {
      if (num == 0) {
        this.musicItemShow = false;
        this.$refs.tabbars.className = "tabbars tabbars-music";
      } else {
        this.musicItemShow = true;
        this.$refs.tabbars.className = "tabbars";
      }
    },
    musicDataRouter(id) {
      if (id.user) {
        this.$emit("get-music-Id", id);
      } else {
        this.$emit("get-music-Id", this.musicId);
      }
    },
    setAppProgress(num) {
      this.$emit("send-App-progress", num);
    },
    sendApp(num) {
      this.$emit("send-app-play", num);
    },
    sendAppLoad() {
      this.$emit("send-app-load");
    },
  },
};
</script>
<style lang="scss" scoped>
#app {
  width: 100vw;
  height: 100vh;
  .tabbars-music {
    height: 50px !important;
  }
  .tabbars {
    z-index: 999;
    position: fixed;
    bottom: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: #fff;
    .music {
      box-shadow: 0 -2px 5px rgb(237, 237, 237);
      position: sticky;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      padding: 0 30px;
      width: 100vw;
      height: 30px;
      background-color: #fff;
      img {
        position: absolute;
        top: -10px;
        left: 35px;
        width: 36px;
        height: 36px;
        border-radius: 999px;
        box-shadow: 0 0 8px rgb(236, 234, 234);
      }
      p {
        width: 100%;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        font-weight: 500;
        font-size: 13px;
        color: #000;
        margin: 5px 10px 0 65px;
      }
      span {
        margin-top: 11px;
        vertical-align: text-bottom;
        display: inline-block;
        width: 22px;
        height: 22px;
        margin-right: 15px;
      }
      .pause {
        background-image: url(../assets/imgs/images/songBegin.png);
        background-size: contain;
        background-repeat: no-repeat;
      }
      .play {
        background-image: url(../assets/imgs/images/stop.png);
        background-size: contain;
        background-repeat: no-repeat;
      }
    }
    .router {
      position: sticky;
      height: 50px;
      display: flex;
      flex-direction: row;
      justify-content: space-evenly;
      align-items: center;
      border-top: 1px solid #c9c9c9;

      a div {
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        font-size: 12px;
        justify-content: space-evenly;
        color: rgb(152, 152, 152);
        &.router-link-exact-active {
          color: var(--theme-green);
        }
      }
      a div i {
        width: 17px;
        height: 20px;
        padding-top: 25px;
        background-size: 17px 20px;
        background-repeat: no-repeat;
      }

      a:first-child i {
        background-image: url(../assets/imgs/images/bottomIcon/sy.png);
      }
      a:first-child.router-link-exact-active i {
        background-image: url(../assets/imgs/images/bottomIcon/syed.png);
      }
      a:nth-of-type(2) i {
        background-image: url(../assets/imgs/images/bottomIcon/xt.png);
      }
      a:nth-of-type(2).router-link-exact-active i {
        background-image: url(../assets/imgs/images/bottomIcon/xted.png);
      }
      a:nth-of-type(3) i {
        width: 30px;
        height: 26px;
        background-size: 30px 26px;
        background-image: url(../assets/imgs/images/midBtn.png);
      }
      a:nth-of-type(4) i {
        background-image: url(../assets/imgs/images/bottomIcon/sc.png);
      }
      a:nth-of-type(4).router-link-exact-active i {
        background-image: url(../assets/imgs/images/bottomIcon/sced.png);
      }
      a:last-child i {
        background-image: url(../assets/imgs/images/bottomIcon/wd.png);
      }
      a:last-child.router-link-exact-active i {
        background-image: url(../assets/imgs/images/bottomIcon/wded.png);
      }
    }
  }
}
/*nav a.router-link-exact-active {*/
/*  color: #42b983;*/
/*}*/
</style>
