import { Overlay, Circle, Slider, PullRefresh, Swipe, SwipeItem, Lazyload, NavBar, ShareSheet, Toast, Search, Tab, Tabs, Button, Popup, Cell } from 'vant'

const UIComponents = [Overlay, Circle, Slider, PullRefresh, Swipe, SwipeItem, Lazyload, NavBar, ShareSheet, Toast, Search, Tab, Tabs, Button, Popup, Cell]


const WotUIPlugin = {};


WotUIPlugin.install = function (Vue) {

    UIComponents.forEach(Component => {

        Vue.use(Component)

    })

}

export default WotUIPlugin