<template>
  <div class="slidings">
    <p @click="routerPage">
      听往期<img src="../assets/imgs/images/more2.png" />
    </p>
    <div class="shadow">
      <van-swipe
        class="sliding-box"
        @change="onChange"
        :show-indicators="false"
      >
        <van-swipe-item
          class="sliding-item"
          v-for="item in slidingCarousel"
          :key="item.id"
        >
          <div class="img-box" @click="playBannerMusic(item.id)">
            <img class="sliding-img" :src="item.str_img_url" />
            <span>第{{ item.n_periods_num }}期</span>
            <i></i>
          </div>
          <p>{{ item.str_program_title }}</p>
        </van-swipe-item>
      </van-swipe>
      <ul class="dots">
        <li
          v-for="(item, i) in slidingCarousel"
          :key="i"
          :class="{ dotactive: current == i }"
        ></li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  name: "SlidingsComponent",
  data() {
    return {
      slidingCarousel: [],
      musicPlayId: [],
      current: 0,
    };
  },
  created() {
    this.$nextTick(() => {
      this.getSildingCarousel();
    });
  },
  methods: {
    async getSildingCarousel() {
      let data = await this.$axios({
        url: "https://app.thepoemforyou.com/api/v1/getNewsProgramNew",
      });
      this.slidingCarousel = data.data.data;
    },
    onChange(index) {
      this.current = index;
    },
    playBannerMusic(id) {
      this.$emit("play-banner-music", id);
    },
    routerPage() {
      this.$router.push({
        path: "/type3",
      });
    },
  },
};
</script>
<style lang="scss" scoped>
@font-face {
  font-family: "fzqk";
  src: url("../assets/imgs/fzqk.TTF");
}

.slidings {
  width: 100vw;
  padding: 0 20px;
  p {
    text-align: right;
    margin: 8px 0;
    color: #999;
    img {
      width: 5px;
      height: 10px;
      margin-left: 5px;
      vertical-align: middle;
    }
  }
  .shadow {
    box-shadow: 0px 0 5px var(--theme-shadow);
    border-radius: 10px;
    padding-bottom: 5px;
    overflow: hidden;
  }

  .sliding-box {
    width: 100%;
    height: 200px;
    overflow: hidden;
    .sliding-item {
      display: inline-block;
      .img-box {
        position: relative;
        display: flex;
        align-items: center;
        width: 100%;
        height: 150px;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        overflow: hidden;
        .sliding-img {
          width: 100%;
          display: inline-block;
        }
        span {
          position: absolute;
          right: 0;
          top: 30px;
          width: max-content;
          padding: 5px 15px 5px 20px;
          background-color: #fff;
          color: var(--theme-green);
          border-top-left-radius: 20px;
          border-bottom-left-radius: 20px;
        }
        i {
          position: absolute;
          left: 42%;
          top: 40%;
          width: 40px;
          height: 40px;
          background-image: url(../assets/imgs/images/beginIndex.png);
          background-repeat: no-repeat;
          background-size: contain;
        }
      }
      p {
        color: #000;
        margin-top: 20px;
        text-align: center;
        font-size: 13px;
        font-family: fzqk;
      }
    }
  }
  .dots {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    li {
      width: 5px;
      height: 5px;
      background-color: #ddd;
      border-radius: 999px;
      margin: 3px;
    }
    .dotactive {
      background-color: #999;
    }
  }
}
</style>