<script>
import ModuleType11 from "@/components/ModuleType11.vue";
import ModuleTypeHeader from "@/components/ModuleTypeHeader.vue";
import ModuleType2 from "@/components/ModuleType2.vue";
import DeblockSection from "@/components/DeblockSection.vue";
export default {
  components: { ModuleType11, ModuleTypeHeader, ModuleType2, DeblockSection },
  data() {
    return {
      allData: [],
      isclassify: this.$route.params.isclassify,
      listOjb: {
        type: 2,
        moreType: 2,
        moduleTitle: "会员免费课程",
      },
      vipData: [],
      sectionList: [],
      // vipSectionTitle:[
      //   // {}
      // ],
      vipSection: {
        enjoy: [], //16
        album: [], //11
        children: [], //12
        classic: [], //13
        poetic: [], // 14
        confucian: [], // 17
        saying: [], //18
        literature: [], //15
      },
    };
  },
  created() {
    this.$nextTick(() => {
      this.getAllData();
      this.getSectionListData();
    });
  },
  methods: {
    goback() {
      this.$router.go(-1);
    },
    async getAllData() {
      let data = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getVipList",
      });
      this.allData = data.data.data;
      let data1 = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getVipFreeAlbum",
        data: {
          page: undefined,
          limit: 2000,
          str: "",
        },
      });
      this.vipData = data1.data.data.data;
      data1.data.data.data.map((item) => {
        if (item.classify_id == 16) {
          this.vipSection.enjoy.push(item);
        } else if (item.classify_id == 11) {
          this.vipSection.album.push(item);
        } else if (item.classify_id == 12) {
          this.vipSection.children.push(item);
        } else if (item.classify_id == 13) {
          this.vipSection.classic.push(item);
        } else if (item.classify_id == 14) {
          this.vipSection.poetic.push(item);
        } else if (item.classify_id == 15) {
          this.vipSection.literature.push(item);
        } else if (item.classify_id == 17) {
          this.vipSection.confucian.push(item);
        } else if (item.classify_id == 18) {
          this.vipSection.saying.push(item);
        }
      });
    },
    async getSectionListData() {
      let data = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getLearnAlbumList",
      });
      this.sectionList = data.data.data;
    },
  },
};
</script>

