<template>
  <div class="home">
    <header>
      <div class="search" @click="searchRouter">
        <input type="search" placeholder="搜索你喜欢的嘉宾、诗歌、主题" />
      </div>
      <img src="../../assets/imgs/images/noImage.png" @click="calendar" />
    </header>
    <div class="header-tab">
      <span>推荐</span>
      <span @click="typePage(4)">栖息岛</span>
      <span @click="typePage(7)">读诗人</span>
      <span @click="typePage(2)">电台</span>
      <span @click="typePage(9, true)"
        >会员<img src="../../assets/imgs/images/VIPlogo2.png"
      /></span>
      <span @click="typePage(1)">动听榜</span>
    </div>
    <SlidingsComponent
      class="slidings"
      @play-banner-music="playBannerMusic"
    ></SlidingsComponent>
    <div class="carefully">
      <p>精选</p>
      <div class="banners">
        <van-swipe
          class="banner-box"
          :autoplay="3000"
          :show-indicators="false"
          style="height: 127px"
        >
          <van-swipe-item
            class="banner-item"
            v-for="item in banners"
            :key="item.banner_name"
            @click="pageRouter(item)"
          >
            <img class="banner-img" v-lazy="item.image_url" />
          </van-swipe-item>
        </van-swipe>
      </div>
    </div>
    <section>
      <div class="seciton-item" v-for="item in section" :key="item.moduleTitle">
        <ModuleTypeHeader :moduleObjItem="item"></ModuleTypeHeader>
        <component
          :is="moduleType[item.moduleType - 1]"
          :moduleObj="item"
        ></component>
      </div>
    </section>
    <div class="blank"></div>
  </div>
</template>

<script>
import SlidingsComponent from "@/components/SlidingsComponent";
import ModuleTypeHeader from "@/components/ModuleTypeHeader.vue";

export default {
  name: "HomeView",
  components: {
    SlidingsComponent,
    ModuleType1: () => import("@/components/ModuleType1.vue"),
    ModuleType2: () => import("@/components/ModuleType2.vue"),
    ModuleType3: () => import("@/components/ModuleType3.vue"),
    ModuleType6: () => import("@/components/ModuleType6.vue"),
    ModuleType7: () => import("@/components/ModuleType7.vue"),
    ModuleType8: () => import("@/components/ModuleType8.vue"),
    ModuleType9: () => import("@/components/ModuleType9.vue"),
    ModuleType11: () => import("@/components/ModuleType11.vue"),
    ModuleTypeHeader,
  },
  data() {
    return {
      banners: [],
      section: [],
      moduleType: [
        "ModuleType1",
        "ModuleType2",
        "ModuleType3",
        "",
        "",
        "ModuleType6",
        "ModuleType7",
        "ModuleType8",
        "ModuleType9",
        "",
        "ModuleType11",
      ],
    };
  },
  created() {
    this.$nextTick(() => {
      this.getBanners();
      this.getSection();
    });
  },
  methods: {
    async getBanners() {
      let data = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getBannerList",
        data: {
          position: 1,
        },
        header: {
          "Content-Type": "application/x-www-form-urlencoded",
        },
      });
      this.banners = data.data.data;
    },
    async getSection() {
      let data = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getIndexAlbumList",
      });
      this.section = data.data.data;
    },
    playBannerMusic(id) {
      this.$router.push({
        path: "/detailMusic",
        query: {
          id,
        },
      });
    },
    searchRouter() {
      this.$router.push({ path: "/search" });
    },
    calendar() {
      this.$router.push({
        path: "/calendar",
      });
    },
    typePage(num, isclassify) {
      this.$router.push({
        name: `type${num}`,
        params: {
          isclassify,
        },
      });
    },
    pageRouter(item) {
      if (item.link_type == 1) {
        let text = item.link_url;
        let id = text.split("id=")[1];
        this.$router.push({
          path: "/detailMusic",
          query: {
            id: id,
          },
        });
      } else {
        this.$router.push({
          path: "/loadApp",
        });
      }
    },
  },
};
</script>

