<template>
  <div id="record">
    <header>
      <img @click="gohome" src="../../assets/imgs/images/pre.png" />
      <van-search
        placeholder="输入诗人、诗歌和标题"
        shape="round"
        class="record-input"
        @click="routerSearch"
      />
      <img src="../../assets/imgs/images/myOpus.png" />
    </header>
    <div class="classify">
      <span class="class-item">
        <ul>
          <li
            v-for="item in classifyTitleList"
            :key="item.id"
            @click="recordSectionPoem(item.id, item.poem_classify_name)"
          >
            {{ item.poem_classify_name }}
          </li>
        </ul>
      </span>
      <span class="more-classify" @click="routerMore">
        更多分类
        <img src="../../assets/imgs/images/next.png" />
      </span>
    </div>
    <div class="banner">
      <van-swipe :autoplay="3000" style="height: 142px" indicator-color="#000">
        <van-swipe-item
          v-for="(image, index) in bannersImg"
          :key="index"
          @click="routerPage(image)"
        >
          <img v-lazy="image.image_url" />
        </van-swipe-item>
      </van-swipe>
    </div>
    <section class="content1">
      <h3>{{ contentTitle1 }}</h3>
      <div class="content1-text">
        <div
          class="text-item"
          v-for="item in content1"
          :key="item.id"
          @click="itemPage(item)"
        >
          <div class="item-left"><img :src="item.iamge" /></div>
          <div class="item-center">
            <h3>{{ item.title }}</h3>
            <p>{{ item.author_name }}</p>
            <p>{{ item.original }}</p>
          </div>
          <div class="item-right">
            <span>朗读</span>
            <span
              ><img src="../../assets/imgs/images/fire.png" />{{
                item.look_count
              }}人读过</span
            >
          </div>
        </div>
      </div>
    </section>
    <RecordSection></RecordSection>
  </div>
</template>
<script>
import RecordSection from "@/components/RecordSection.vue";
export default {
  components: { RecordSection },
  name: "RecordView",
  data() {
    return {
      classifyTitleList: [],
      bannersImg: [],
      contentTitle1: "",
      content1: [],
    };
  },
  created() {
    this.$nextTick(() => {
      this.classify();
      this.banners();
      this.content();
      this.contentTitle();
    });
  },
  methods: {
    async classify() {
      let data = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getPoemClassifyListApp",
        data: {
          page: 1,
          limit: 12,
          str: "",
        },
      });
      this.classifyTitleList = data.data.data.data;
    },
    async banners() {
      let data = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getBannerList",
        data: {
          position: 4,
        },
      });
      this.bannersImg = data.data.data;
    },
    async contentTitle() {
      let data1 = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getReadBookTitle",
        data: {
          type: 8,
        },
      });
      this.contentTitle1 = data1.data.data;
    },
    async content() {
      let data1 = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getRecommendPoem",
      });
      this.content1 = data1.data.data;
    },
    routerPage(item) {
      let text = item.link_url.split("?")[0];
      let url1 = "/pages/bookAlbum/bookAlbum";
      let url2 = "/pages/albumDetail/albumDetail";
      let id = -1;
      if (text == url1) {
        id = Number(item.link_url.split("id=")[1]);
        this.$router.push({
          path: "/record/bannerpage",
          query: {
            id,
          },
        });
      } else if (text == url2) {
        id = Number(item.link_url.split("id=")[1]);
        this.$router.push({
          name: "section",
          params: {
            type: 3,
            id,
          },
        });
      }
    },
    itemPage(item) {
      this.$router.push({
        path: "/record-poem",
        query: {
          item,
        },
      });
    },
    recordSectionPoem(id, title) {
      this.$router.push({
        path: "/record-section-poem",
        query: {
          id,
          title,
        },
      });
    },
    routerMore() {
      this.$router.push({
        path: "/record-section-more",
      });
    },
    routerSearch() {
      this.$router.push({
        path: "/record-search",
        query: {
          id: 0,
        },
      });
    },
    gohome() {
      this.$router.go(-1);
    },
  },
};
</script>
<style lang="scss" scoped>
@font-face {
  font-family: "fzqk";
  src: url("../../assets/imgs/fzqk.TTF");
  font-family: "dcloud_iconfont";
  src: url("../../assets/fonts/dcloud_iconfont.ttf");
}
#record {
  header {
    padding: 0 20px;
    position: sticky;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    background-color: #fff;
    z-index: 999;
    img {
      width: 15px;
      &:first-child {
        width: 10px;
      }
    }
    .record-input {
      flex: 1;
    }
  }
  .classify {
    padding: 0 20px;
    position: relative;
    margin-top: 8px;
    .class-item {
      margin-left: 15px;
      background-color: #fff;
      position: absolute;
      left: 0;
      top: 0;
      width: 70%;
      font-size: 13px;
      overflow: scroll;
      &::-webkit-scrollbar {
        display: none;
      }
      ul {
        min-width: max-content;
        display: flex;
        flex-direction: row;
        li {
          padding: 10px 15px;
          background-color: rgb(195, 193, 193, 0.5);
          margin-right: 10px;
          border-radius: 999px;
        }
      }
    }
    .more-classify {
      position: absolute;
      right: 0;
      top: 0;
      padding: 10px 0 10px 10px;
      width: 25%;
      font-size: 13px;
      line-height: 15px;
      background-color: #fff;
      color: var(--theme-gray);
      img {
        width: 5px;
        margin-left: 5px;
      }
    }
  }
  .banner {
    margin-top: 50px;
    width: 100%;
    padding: 20px;
    &::v-deep img {
      width: 100%;
    }
  }
  .content1 {
    padding: 0 20px;
    h3 {
      font-family: fzqk;
      font-size: 15px;
    }
  }

  .content1 {
    .content1-text {
      .text-item {
        position: relative;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        margin: 30px 0;
        padding: 15px;
        box-shadow: 0px 0px 8px var(--theme-shadow);
        border-radius: 8px;
        .item-left {
          position: absolute;
          top: -15px;
          left: 15px;
          width: 22%;
          height: 100%;
          border-radius: 8px;
          overflow: hidden;
          img {
            width: 100%;
            height: 100%;
          }
        }
        .item-center {
          margin-left: 29%;
          width: 45%;
          h3 {
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            font-size: 15px;
            font-weight: 500;
          }
          p {
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            font-size: 13px;
            color: rgb(147, 140, 140);
            margin: 8px 0;
            &:first-child {
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
              font-size: 12px;
              color: var(--theme-gray);
            }
          }
        }
        .item-right {
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: space-between;
          span {
            font-size: 12px;
            color: var(--theme-gray);
            &:first-child {
              // font-size: 18px;
              padding: 8px 12px;
              border-radius: 999px;
              border: 1px solid var(--theme-green);
              color: var(--theme-green);
            }
            img {
              width: 10px;
              height: 10px;
              margin-top: 5px;
              margin-right: 5px;
            }
          }
        }
      }
    }
  }
}
</style>